import React, { useState, useRef, useEffect } from "react";
import { useFormik } from "formik";
import ApiPost from "../Api/Post";

const useAddNurseForm = (callback) => {
  const [toEdit, setToEdit] = useState(false);
  const [toBack, setToBack] = useState(false);
  const [toCustomerSupportRequest, setToCustomerSupportRequest] = useState(
    false
  );
  const [popup, setToPopup] = useState(false);
  const [toRemove, setToRemove] = useState(false);
  const [unitData, setUnitData] = useState([]);
  const [toOpenSuspend, setOpenSuspend] = useState(false);
  const imageInput = useRef(null);
  const [image, setImage] = useState("");
  const [showUnits, setShowUnits] = useState(false);
  const formik = useFormik({
    initialValues: {
      firstName: "",
      lastName: "",
      email: "",
      phoneNumber: "",
      title: "",
      certifications: "",
      experience: "",
      hospitalName: sessionStorage.getItem("hospitalName"),
      startDate: "",
      units: "",
      password: "",
      role: "",
    },
    onSubmit: (values) => {
      if (toEdit === false) {
        handleAddStaff(values);
      } else {
        handleupdateStaff(values);
      }
    },
    onReset: (values) => {},
  });

  useEffect(() => {
    if (sessionStorage.getItem("staffId") != null) {
      setToEdit(true);
    }
  }, []);
  const handleClickRemove = () => {
    setToPopup(true);
  };
  const handleClose = () => {
    setToPopup(false);
  };
  const handleClickSuspend = () => {
    setOpenSuspend(true);
  };
  const handleClickChargeNurse = () => {
    setToPopup(true);
  };
  const handleCloseSuspend = () => {
    setOpenSuspend(false);
  };
  const handleClickCustomerRequest = () => {
    setToCustomerSupportRequest(true);
  };
  // Function to handle profile uploads
  const handleImageClick = (e) => {
    imageInput.current.click();
  };
  const handleImageChange = (e) => {
    e.preventDefault();
    const reader = new FileReader();
    const img = e.target.files[0];
    if (img) {
      reader.readAsDataURL(img);
    }
    reader.onloadend = () => {
      setImage(reader.result);
    };
  };
  const handleDelete = (unitToDelete) => () => {
    setUnitData((units) => units.filter((item) => item !== unitToDelete));
  };
  const handleAddUnits = (event) => {
    if (event) event.preventDefault();
    if (formik.values.units !== "") {
      setUnitData((units) => [...units, formik.values.units]);
    }
    setShowUnits(true);
    formik.setFieldValue("units", "");
  };
  const handleBack = () => {
    setToBack(true);
  };
  const handleClickChargeNurseOk = () => {
    formik.setFieldValue("role", "charge_nurse");
    setToPopup(false);
  };
  const handleAdmin = () => {
    formik.setFieldValue("role", "admin");
  };
  const handleNone = () => {
    formik.setFieldValue("role", "none");
  };
  const handleOpenRemove = () => {
    setToRemove(true);
  };
  const handleCloseRemove = () => {
    setToRemove(false);
  };

  async function handleAddStaff(values) {
    debugger;
    const id = sessionStorage.getItem("hospitalId");
    fetch("http://3.142.32.39/api/v1/hospital/" + id + "/staff", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
      body: JSON.stringify({
        email: values.email,
        password: values.password,
        phone: values.phoneNumber,
        first_name: values.firstName,
        last_name: values.lastName,
        title: values.title,
        roles: values.role,
        certifications: values.certifications,
        experience: values.experience,
        start_date: values.startDate,
        sms_notifications: false,
        profile_picture: image,
        active: true,
        type: "hospital_staff",
      }),
    }).then((res) => res.json())
      .then((res) => {
        console.log(res.data);
      })
      .catch((error) => {
        console.log(error.response.data);
      });
    // const response = await fetch(
    //   "http://3.142.32.39/api/v1/hospital/" + parseInt(id) + "/staff",
    //   {
    //     method: "POST",
    //     headers: {
    //       Accept: "application/json",
    //       Authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
    //     },
    //     body: JSON.stringify({
    //       email: values.email,
    //       password: values.password,
    //       phone: values.phoneNumber,
    //       first_name: values.firstName,
    //       last_name: values.lastName,
    //       title: values.title,
    //       roles: values.role,
    //       certifications: values.certifications,
    //       experience: values.experience,
    //       start_date: values.startDate,
    //       sms_notifications: false,
    //       profile_picture: image,
    //       active: true,
    //       type: "hospital_staff",
    //     }),
    //   }
    // );
    // const responseJson = await response.json();
    // if (responseJson.code === "HOSPITAL_STAFF_CREATED") {
    //   setTimeout(() => {
    //     setToBack(true);
    //   }, 100);
    // }
  }
  async function handleupdateStaff(values) {
    const hospitalId = sessionStorage.getItem("hospitalId");
    const staffId = sessionStorage.getItem("staffId");
    const response = await fetch(
      ApiPost.updateStaff + "/" + hospitalId + "/nurses" + "/" + staffId,
      {
        method: "PUT",
        headers: {
          "content-type": "application/json",
          Authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
        body: JSON.stringify({
          email: values.email,
          password: values.password,
          phone_no: values.phoneNumber,
          first_name: values.firstName,
          last_name: values.lastName,
          title: values.title,
          role_id: values.roleId,
          certifications: values.certifications,
          experience: values.experience,
          hospital_id: 3,
          start_date: values.startDate,
          profile_picture: image,
        }),
      }
    );
    const responseJson = await response.json();
  }
  return {
    formik,
    toEdit,
    handleClose,
    popup,
    handleClickSuspend,
    handleClickChargeNurse,
    toOpenSuspend,
    handleCloseSuspend,
    handleClickCustomerRequest,
    toCustomerSupportRequest,
    imageInput,
    handleImageClick,
    handleImageChange,
    image,
    unitData,
    handleDelete,
    handleAddUnits,
    showUnits,
    toBack,
    handleBack,
    handleClickChargeNurseOk,
    toRemove,
    handleOpenRemove,
    handleCloseRemove,
    handleAdmin,
    handleNone,
  };
};

export default useAddNurseForm;
