import React from "react";
import {
  Button,
  Typography,
  TextField,
  Grid,
  Container,
  Paper,
  Dialog,
  DialogContent,
  DialogContentText,
  Tab,
  Tabs,
  Divider,
} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import SearchIcon from "@material-ui/icons/Search";
import { Link } from "react-router-dom";
import { SketchPicker } from "react-color";
import variables from "../../variables.module.scss";
import useEditHospitalForm from "./useEditHospitalForm";
import useAddHospitalForm from "./useAddHospitalForm";
import { makeStyles } from "@material-ui/core/styles";
import "../../assets/style/styles.scss";

const EditHospitals = () => {
  const {
    formik,
    toDelete,
    toInactive,
    handleCloseDelete,
    handleOpenDelete,
    handleOpenInactive,
    handleCloseInactive,
    handleChange,
    value,
    buttonId,
    primaryColor,
    handleAddAdmin,
    handleAdminChange,
    handleDeleteAdmin,
    adminData,
    adminName,
    handleDialogClickOpen,
    handleClose,
    toOpenDialog,
  } = useEditHospitalForm();

  const { handleClickOpen, handleColorChange, toOpen } = useAddHospitalForm();

  const useStyles = makeStyles({
    mainHd: {
      fontFamily: variables.sfProText,
      color: variables.dimGrayColor,
    },
    hpName: {
      color: variables.darkColor,
      fontFamily: variables.sfProDisplay,
      fontSize: "34px",
      lineHeight: "41px",
      fontWeight: variables.fontwt600,
      display: variables.display,
    },
    tabMainContainer: {
      marginTop: "1%",
      width: variables.fullWidth,
    },
    tabDetails: {
      fontFamily: variables.sfProText,
      width: "100%",
      borderBottom: "4px solid #D6D6D6",
    },
    indicator: {
      backgroundColor: variables.tertiaryColor,
      height: "4px",
    },
    hpBin: {
      color: variables.darkColor,
      fontFamily: variables.sfProText,
      fontSize: "15px",
      lineHeight: "18px",
      fontWeight: variables.fontwt600,
      display: variables.display,
      fontStyle: "normal",
      paddingTop: "2%",
      paddingBottom: "2%",
      paddingLeft: "3%",
    },
    hpPoupmain: {
      color: variables.darkColor,
      fontFamily: variables.sfProText,
      fontSize: "28px",
      textAlign: "center",
      fontWeight: variables.fontwt500,
      lineHeight: "38px",
      fontStyle: "normal",
      left: "173.5px",
    },
    hpPoupsub: {
      color: variables.dimGrayColor,
      fontFamily: variables.sfProText,
      fontSize: "18px",
      textAlign: "center",
      fontWeight: variables.fontwt500,
      lineHeight: "21px",
    },
    hpAdmin: {
      color: variables.darkColor,
      fontFamily: variables.sfProText,
      fontSize: "28px",
      lineHeight: "33px",
      textAlign: "center",
    },
    hpInf: {
      color: variables.darkColor,
      fontFamily: variables.sfProDisplay,
      fontSize: "22px",
      lineHeight: "26px",
      fontWeight: variables.fontwt600,
      display: "flex",
      fontStyle: "normal",
    },
    hpLabel: {
      fontWeight: variables.fontwt500,
      fontSize: variables.fontSize12,
      color: variables.darkColor,
      fontFamily: variables.sfProText,
      paddingTop: "1%",
    },
    //primary color button
    primaryBtn: {
      width: variables.width,
      height: variables.height,
      background: primaryColor.primaryColor,
      "&:hover": {
        backgroundColor: primaryColor.primaryColor,
      },
    },
    //secondary color button
    secondaryBtn: {
      width: variables.width,
      height: variables.height,
      background: primaryColor.secondaryColor,
      "&:hover": {
        backgroundColor: primaryColor.secondaryColor,
      },
    },
    //tertiary color button
    tertiaryBtn: {
      width: variables.width,
      height: variables.height,
      background: primaryColor.tertiaryColor,
      "&:hover": {
        backgroundColor: primaryColor.tertiaryColor,
      },
    },
    //color selector  button
    colorBtn: {
      width: variables.width,
      height: variables.height,
      border: "1px solid #D6D6D6",
      borderRadius: "4px",
      backgroundColor: variables.grayColor,
      "&:hover": {
        backgroundColor: variables.grayColor,
      },
    },
    hpDeletebtn: {
      width: "211px",
      height: "55px",
      border: "1px solid #212121",
      background: variables.whiteColor,
      fontFamily: variables.sfProText,
      "&:hover": {
        backgroundColor: variables.whiteColor,
      },
      borderRadius: variables.borderRadius,
      left: "10 %",
    },
    hpInactivebtn: {
      width: "211px",
      height: "55px",
      border: "1px solid #212121",
      borderStyle: "none",
      borderRadius: variables.borderRadius,
      fontFamily: variables.sfProText,
      backgroundColor: variables.dimGrayColor,
      "&:hover": {
        backgroundColor: variables.dimGrayColor,
      },
      color: variables.whiteColor,
    },
    adminHd: {
      color: variables.darkColor,
      fontWeight: variables.fontwt600,
      fontFamily: variables.sfProDisplay,
      fontSize: "22px",
      lineHeight: "26px",
    },
    addHd: {
      color: variables.darkColor,
      fontWeight: variables.fontWeight,
      fontFamily: variables.sfProText,
      fontSize: "12px",
      lineHeight: "14px",
    },
    addnewBtn: {
      width: "87px",
      height: "50px",
      fontFamily: variables.sfProText,
      backgroundColor: variables.tertiaryColor,
      "&:hover": {
        backgroundColor: variables.tertiaryColor,
      },
      color: variables.whiteColor,
    },
    hpGrid: {
      marginLeft: "4%",
      marginTop: "2%",
    },
    subGrid: {
      marginTop: "2%",
    },
    grid: {
      paddingTop: "2%",
    },
    colorGrid: {
      marginLeft: "2%",
      marginTop: "1%",
    },
    btnGrid: {
      paddingTop: "1%",
      paddingBottom: "1%",
    },
    backBtn: {
      borderRadius: "10px",
    },
    icon: {
      color: variables.secondaryColor,
    },
    adminInfo: {
      marginLeft: "23%",
      marginTop: "2%",
    },
    adminGrid: {
      marginTop: "2%",
      overflowY: "scroll",
      height: "250px",
    },
    adminName: {
      color: variables.secondaryColor,
      fontFamily: variables.sfProText,
      fontWeight: "bold",
    },
    adminDetails: {
      color: variables.darkColor,
      fontFamily: variables.sfProText,
    },
    deleteBtn: {
      color: variables.grayColor,
      textTransform: "lowercase",
      fontFamily: variables.sfProText,
    },
    autoList: {
      width: "286px",
    },
    autoName: {
      "&::placeholder": {
        fontSize: variables.fontSize15,
        fontWeight: variables.fontwt500,
        color: variables.darkColor,
        paddingLeft: "1%",
        fontFamily: variables.sfProText,
      },
    },
    paper: {
      borderRadius: "16px",
    },
  });

  const classes = useStyles();

  return (
    <>
      <Container maxWidth="lg">
        <Grid container className={classes.grid}>
          <Button edge="start" variant="outlined" className="btn-small">
            <SearchIcon className={classes.icon}></SearchIcon>
          </Button>
        </Grid>
        <Grid className={classes.grid}>
          <Typography className={classes.mainHd}> Basic Information</Typography>
        </Grid>
        <Grid>
          <Typography className={classes.hpName}>
            {sessionStorage.getItem("hospitalName")}
          </Typography>
        </Grid>
        <Grid className={classes.tabMainContainer}>
          <Tabs
            value={value}
            //indicatorColor="primary"
            textColor="primary"
            onChange={handleChange}
            classes={{ indicator: classes.indicator }}
          >
            <Tab
              label="UNITS"
              to="/Units"
              component={Link}
              className={classes.tabDetails}
            />
            <Tab
              label="STAFF"
              className={classes.tabDetails}
              to="/Nurses"
              component={Link}
            />
            <Tab label="PATIENTS" disabled className={classes.tabDetails} />
            <Tab label="BASIC INFORMATION" className={classes.tabDetails} />
          </Tabs>
        </Grid>
        <Grid className={classes.grid}>
          <Paper className={classes.paper}>
            <Grid>
              <Typography className={classes.hpBin}>
                Basic Information
              </Typography>
            </Grid>
            <Divider className="divider" />
            <Grid container>
              <Grid item sm={6}>
                <Grid className={classes.hpGrid}>
                  <Typography className={classes.hpInf}>
                    Hospital Information
                  </Typography>
                  <Typography className={classes.hpLabel}>
                    HOSPITAL NAME
                  </Typography>
                  <input
                    name="name"
                    type="text"
                    placeholder="HOSPITAL NAME"
                    className="textInput-large"
                    value={formik.values.name}
                    onChange={formik.handleChange}
                  />
                  <Typography className={classes.hpLabel}>ADDRESS</Typography>
                  <input
                    id="address"
                    type="text"
                    name="address"
                    placeholder="ADDRESS"
                    className="textInput-large"
                    value={formik.values.address}
                    onChange={formik.handleChange}
                  />
                  <Grid container spacing={2}>
                    <Grid item sm={5}>
                      <Typography className={classes.hpLabel}>CITY</Typography>
                      <input
                        id="city"
                        name="city"
                        type="text"
                        className="textInput-small"
                        placeholder="CITY"
                        value={formik.values.city}
                        onChange={formik.handleChange}
                      />
                    </Grid>
                    <Grid item sm={6}>
                      <Grid>
                        <Typography className={classes.hpLabel}>
                          STATE
                        </Typography>
                        <input
                          type="text"
                          id="state"
                          name="state"
                          placeholder="STATE"
                          className="textInput-small"
                          value={formik.values.state}
                          onChange={formik.handleChange}
                        />
                      </Grid>
                    </Grid>
                  </Grid>
                  <Typography className={classes.hpLabel}>ZIP CODE</Typography>
                  <input
                    id="zipcode"
                    name="zipcode"
                    type="text"
                    placeholder="ZIP CODE"
                    className="textInput-small"
                    value={formik.values.zipcode}
                    onChange={formik.handleChange}
                  />
                  <Typography className={classes.hpLabel}>
                    BRAND COLORS
                  </Typography>
                  <Grid container spacing={1}>
                    <Grid item>
                      {primaryColor.primaryColor !== "" && (
                        <Button
                          className={classes.primaryBtn}
                          id="primary"
                          name="primary"
                        ></Button>
                      )}
                    </Grid>
                    <Grid item>
                      {primaryColor.secondaryColor !== "" && (
                        <Button
                          id="secondary"
                          name="secondary"
                          className={classes.secondaryBtn}
                        ></Button>
                      )}
                    </Grid>
                    <Grid item>
                      {primaryColor.tertiaryColor !== "" && (
                        <Button
                          id="tertiary"
                          name="tertiary"
                          className={classes.tertiaryBtn}
                        ></Button>
                      )}
                    </Grid>
                    <Grid item>
                      {buttonId !== 3 && (
                        <Button
                          onClick={handleClickOpen}
                          id="Colorbtn"
                          className={classes.colorBtn}
                        >
                          +
                        </Button>
                      )}
                    </Grid>
                  </Grid>
                  <Grid className={classes.colorGrid}>
                    {toOpen == true && (
                      <SketchPicker
                        size={200}
                        onChangeComplete={handleColorChange}
                      />
                    )}
                  </Grid>
                </Grid>
              </Grid>
              {/* Administrator Information */}
              <Grid item sm={6}>
                <Grid className={classes.adminInfo}>
                  <Grid>
                    <Typography className={classes.adminHd}>
                      Administrator Information
                    </Typography>
                  </Grid>
                  <Grid className={classes.adminGrid}>
                    {adminData.map((item) => (
                      <Grid key={item.id}>
                        <Grid>
                          <Typography className={classes.adminName}>
                            {item.first_name + " " + item.last_name}
                          </Typography>
                        </Grid>
                        <Grid>
                          <Typography className={classes.adminDetails}>
                            {item.email}
                          </Typography>
                        </Grid>
                        <Grid>
                          <Typography className={classes.adminDetails}>
                            {item.phone}
                          </Typography>
                        </Grid>
                        <Grid>
                          <Typography className={classes.adminDetails}>
                            {item.type}
                          </Typography>
                        </Grid>
                        <Grid>
                          <Button
                            className={classes.deleteBtn}
                            onClick={() => handleDialogClickOpen(item.id)}
                          >
                            Delete
                          </Button>
                        </Grid>
                        <Divider />
                      </Grid>
                    ))}
                  </Grid>
                  <Grid className={classes.subGrid}>
                    <Typography className={classes.addHd}>
                      ADD NEW ADMIN
                    </Typography>
                  </Grid>
                  <Grid
                    container
                    className={classes.subGrid}
                    spacing={1}
                    alignItems="center"
                  >
                    <Grid item>
                      <Autocomplete
                        id="admin"
                        options={adminName.adminName}
                        getOptionLabel={(option) =>
                          option.first_name + "" + option.last_name
                        }
                        className={classes.autoList}
                        onChange={(event, value) =>
                          handleAdminChange(event, value)
                        }
                        className={classes.autoList}
                        freeSolo
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            variant="outlined"
                            placeholder="NAME"
                            className={classes.autoName}
                          />
                        )}
                      />
                    </Grid>

                    <Grid item>
                      <Button
                        className={classes.addnewBtn}
                        onClick={handleAddAdmin}
                      >
                        ADD NEW
                      </Button>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
        <Grid className={classes.btnGrid}>
          <Grid container>
            <Grid item sm={3}>
              <Button
                className={classes.hpDeletebtn}
                onClick={handleOpenDelete}
              >
                DELETE ACCOUNT
              </Button>
            </Grid>
            <Grid item sm={4}>
              <Button
                onClick={handleOpenInactive}
                className={classes.hpInactivebtn}
              >
                MARK AS INACTIVE
              </Button>
            </Grid>
            <Grid>
              <Button onClick={formik.handleSubmit} className="btn-large">
                SAVE
              </Button>
            </Grid>
          </Grid>
        </Grid>
        {/* Delete Administrator  */}
        <Grid>
          <Dialog
            maxWidth={"md"}
            modal={false}
            open={toOpenDialog}
            onRequestClose={handleClose}
          >
            <DialogContent>
              <DialogContentText>
                <Typography className={classes.hpAdmin}>
                  Are you sure you want to delete this administrator ?
                </Typography>
              </DialogContentText>
              <Grid container justify="center" spacing={2}>
                <Grid item>
                  <Button onClick={handleClose} className="btn-cancel">
                    CANCEL
                  </Button>
                </Grid>
                <Grid item>
                  <Button onClick={handleDeleteAdmin} className="btn-delete">
                    DELETE
                  </Button>
                </Grid>
              </Grid>
            </DialogContent>
          </Dialog>
        </Grid>
        {/* Delete Hospital  */}
        <Grid>
          <Dialog
            maxWidth={"lg"}
            modal={false}
            open={toDelete}
            onRequestClose={handleCloseDelete}
          >
            <DialogContent>
              <DialogContentText>
                <Typography className={classes.hpPoupmain}>
                  Are you sure?
                </Typography>
                <Typography className={classes.hpPoupsub}>
                  This will delete this hospital and all its data.
                </Typography>
              </DialogContentText>
              <Grid container justify="center" spacing={2}>
                <Grid item>
                  <Button className="btn-cancel" onClick={handleCloseDelete}>
                    CANCEL
                  </Button>
                </Grid>
                <Grid item>
                  <Button className="btn-delete">DELETE</Button>
                </Grid>
              </Grid>
            </DialogContent>
          </Dialog>
        </Grid>
        {/* Inactive Hospital  */}
        <Grid>
          <Dialog
            maxWidth={"md"}
            modal={false}
            open={toInactive}
            onRequestClose={handleCloseInactive}
          >
            <DialogContent>
              <DialogContentText>
                <Typography className={classes.hpPoupmain}>
                  Are you sure?
                </Typography>
                <Typography className={classes.hpPoupsub}>
                  This will archive this hospital and all its data.
                </Typography>
              </DialogContentText>
              <Grid container justify="center" spacing={2}>
                <Grid item>
                  <Button className="btn-cancel" onClick={handleCloseInactive}>
                    CANCEL
                  </Button>
                </Grid>
                <Grid item>
                  <Button className="btn-delete">ARCHIVE</Button>
                </Grid>
              </Grid>
            </DialogContent>
          </Dialog>
        </Grid>
      </Container>
    </>
  );
};

export default EditHospitals;
