import React, { useState, useEffect } from "react";
import ApiGet from "../Api/Get";
import ApiPost from "../Api/Post";
const useHospitalList = () => {
  var today = new Date();
  var day = today.toLocaleDateString("en-US", { day: "numeric" });
  var month = today.toLocaleDateString("en-US", { month: "long" });
  var year = today.toLocaleDateString("en-US", { year: "numeric" });
  var currentDate = day + " " + month + " " + year;
  const [toOpen, setToOpen] = useState(false);
  const [toAdd, setToAdd] = useState(false);
  const [toEdit, setToEdit] = useState(false);
  const [toOpenSortBy, setToSortBy] = useState(null);
  const [sortOption, setSortOption] = useState();
  const [data, setData] = useState({ hospitalDetails: [] });
  const [values, setValues] = useState({
    hospitalId: "",
  });
  const handleSortOptionChange = (event) => {
    setSortOption(event.target.value);
  };
  useEffect(() => {
    getHospital();
  },[]);
  async function getHospital() {
    let response = await fetch(ApiGet.getHospitals, {
      headers: {
        "content-type": "application/json",
        authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
    });

    let responseJson = await response.json();
    setData({
      hospitalDetails: responseJson.data,
    });
  }
  async function deleteHospital() {
    let response = await fetch(
      ApiPost.deleteHospitals + "/" + values.hospitalId,
      {
        method: "DELETE",
        headers: {
          "content-type": "application/json",
          authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
      }
    );
    let responseJson = await response.json();
    if (responseJson.code == "HOSPITAL_DELETED") {
      setTimeout(() => {
        getHospital();
      }, 100);
    }
    setToOpen(false);
  }

  const handleClickOpen = (id) => {
    setToOpen(true);
    setValues({
      hospitalId: id,
    });
  };
  const handleClose = () => {
    setToOpen(false);
  };
  const handleOpenSortBy = (event) => {
    setToSortBy(event.currentTarget);
  };
  const handleCloseSortBy = (event) => {
    setToSortBy(null);
  };

  const handleAddHospital = () => {
    setToAdd(true);
  };
  const handleEdit = (id,name) => {
    sessionStorage.setItem("hospitalId", id);
    sessionStorage.setItem("hospitalName",name);
    setToEdit(true);
  };
  const handleDelete = () => {
    deleteHospital();
  };
  return {
    currentDate,
    handleAddHospital,
    toAdd,
    data,
    toOpenSortBy,
    handleClickOpen,
    handleOpenSortBy,
    toOpen,
    handleClose,
    sortOption,
    handleSortOptionChange,
    handleCloseSortBy,
    handleEdit,
    toEdit,
    handleDelete,
  };
};

export default useHospitalList;
