import React, { useState } from "react";
import {
  Button,
  Drawer,
  Grid,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { Switch, Route, BrowserRouter, Link, Redirect } from "react-router-dom";
import DashboardIcon from "@material-ui/icons/Dashboard";
import LocationCityIcon from "@material-ui/icons/LocationCity";
import GroupIcon from "@material-ui/icons/Group";
import Hospitals from "./Hospitals/HospitalList";
import Nurses from "./Nurses/NursesForm";
import Users from "./Users/UsersForm";
import Units from "./Units/UnitsList/UnitsList";
import { useIdleTimer } from "react-idle-timer";
import { useHistory } from "react-router-dom";
import Logo from "../images/NightingaleLogo.jpg";
import variables from "../variables.module.scss";
import AddHospital from "./Hospitals/AddHospital";
import EditHospital from "./Hospitals/EditHospital";
import AddNurseForm from "./Nurses/AddNurseForm";
import CustomerSupportRequestForm from "./Nurses/CustomerSupportRequestForm";
import AddUser from "./Users/AddUser";
import AddUnitForm from "./Units/NewUnit/AddUnitForm";
import EditUnitForm from "./Units/EditUnit/EditUnitForm";

const windowHeight = window.innerHeight;
const useStyles = makeStyles((theme) => ({
  whiteColor: {
    color: variables.whiteColor,
  },
  main: {
    margin: 0,
    width: variables.fullWidth,
  },
  sidebar: {
    position: "relative",
    height: windowHeight,
    background: variables.sidebar,
    borderRadius: "0px 16px 16px 0px",
  },
  listItem: {
    borderRadius: "1em",
    color: variables.whiteColor,
  },
  listItemDone: {
    color: variables.whiteColor,
    background: "rgba(255, 255, 255, 0.1)",
    borderRadius: "8px",
    width: "191px",
    height: "55px",
  },
  appContent: {
    backgroundColor: variables.backgroundColor,
  },
  logoutContainer: {
    marginTop: "100%",
  },
  logout: {
    color: variables.whiteColor,
    border: "1px solid rgba(255, 255, 255, 0.4)",
    borderRadius: "8px",
    width: "191px",
    height: "56px",
    textTransform: "none",
  },
  mt10: {
    marginTop: variables.marginTop10,
  },
  drawerPaper: {
    width: drawerWidth,
    backgroundColor: variables.sidebar,
    borderRadius: "0px 16px 16px 0px",
  },
  list: {
    margin: "0 auto",
    paddingTop: "25%",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    backgroundColor: "#f8f8f8",
    marginLeft: drawerWidth,
  },
}));

// Sidebar width
const drawerWidth = 223;
// 15 minutes
const timeout = 1000 * 60 * 15;
// Delay with which idle time checks for last active time
const delay = 500;
const Layout = () => {
  const history = useHistory();
  const [toLogout, setToLogout] = useState(false);
  const [ClickListItem, setClickListItem] = useState("Hospitals");
  const [open, setOpen] = React.useState(false);
  const onClickListItem = (data) => (event) => {
    setClickListItem(data);
    setOpen(false);
  };

  //user is not idle its redirects to login
  const handleOnIdle = (event) => {
    history.push(`/`);
    sessionStorage.clear();
  };
  //timeout
  const { getRemainingTime, getLastActiveTime } = useIdleTimer({
    timeout: timeout,
    onIdle: handleOnIdle,
    debounce: delay,
  });
  const handleLogout = () => {
    setToLogout(true);
    sessionStorage.clear();
  };

  const classes = useStyles();
  return (
    <>
      {toLogout ? <Redirect to="/" /> : null}
      <BrowserRouter>
        <nav className={classes.drawer}>
          <Drawer
            variant="permanent"
            open
            classes={{
              paper: classes.drawerPaper,
            }}
          >
            {/* Logo */}
            <Grid
              container
              justify="center"
              alignItems="center"
              className={classes.mt10}
            >
              <Grid item>
                <img src={Logo}></img>
              </Grid>
            </Grid>

            {/* Menu */}
            <List className={classes.list}>
              <ListItem
                button
                component={Link}
                to="/Dashboard"
                onClick={onClickListItem("Dashboard")}
                className={
                  ClickListItem == "Dashboard"
                    ? classes.listItemDone
                    : classes.whiteColor
                }
              >
                <ListItemIcon>
                  <DashboardIcon className={classes.whiteColor} />
                </ListItemIcon>

                <ListItemText
                  disableTypography
                  className={classes.whiteColor}
                  primary="Dashboard"
                />
              </ListItem>
              <ListItem
                button
                component={Link}
                to="/Users"
                onClick={onClickListItem("Users")}
                className={
                  ClickListItem == "Users"
                    ? classes.listItemDone
                    : classes.whiteColor
                }
              >
                <ListItemIcon>
                  <GroupIcon className={classes.whiteColor} />
                </ListItemIcon>

                <ListItemText
                  disableTypography
                  className={classes.whiteColor}
                  primary="Users"
                />
              </ListItem>
              <ListItem
                button
                component={Link}
                to="/Hospitals"
                onClick={onClickListItem("Hospitals")}
                className={
                  ClickListItem == "Hospitals"
                    ? classes.listItemDone
                    : classes.whiteColor
                }
              >
                <ListItemIcon>
                  <LocationCityIcon className={classes.whiteColor} />
                </ListItemIcon>

                <ListItemText
                  disableTypography
                  className={classes.whiteColor}
                  primary="Hospitals"
                />
              </ListItem>
              <ListItem
                button
                component={Link}
                to="/Settings"
                onClick={onClickListItem("Settings")}
                className={
                  ClickListItem == "Settings"
                    ? classes.listItemDone
                    : classes.whiteColor
                }
              >
                <ListItemIcon>
                  <DashboardIcon className={classes.whiteColor} />
                </ListItemIcon>

                <ListItemText
                  disableTypography
                  className={classes.whiteColor}
                  primary="Settings"
                />
              </ListItem>
            </List>
            {/* Logout Button */}
            <Grid
              container
              justify="center"
              direction="column"
              align="center"
              className={classes.logoutContainer}
            >
              <Grid
                item
                container
                direction="row"
                alignItems="flex-end"
                justify="center"
              >
                <Button
                  variant="outlined"
                  onClick={handleLogout}
                  className={classes.logout}
                >
                  Log Out
                </Button>
              </Grid>
            </Grid>
          </Drawer>
        </nav>
        <main className={classes.content}>
          <Switch>
            <Route path="/Hospitals" exact component={Hospitals} />
            <Route path="/Nurses" component={Nurses} />
            <Route path="/Users" component={Users} />
            <Route path="/Units" component={Units} />
            <Route path="/AddHospital" component={AddHospital} />
            <Route path="/EditHospital" component={EditHospital} />
            <Route path="/AddNurseForm" component={AddNurseForm} />
            <Route path="/AddUser" component={AddUser} />
            <Route path="/AddUnitForm" component={AddUnitForm} />
            <Route path="/EditUnitForm" component={EditUnitForm} />
            <Route
              path="/CustomerSupportRequestForm"
              component={CustomerSupportRequestForm}
            />
          </Switch>
        </main>
      </BrowserRouter>
    </>
  );
};

export default Layout;
