import React from "react";
import {
  Button,
  Grid,
  Typography,
  IconButton,
  Container,
  Paper,
  Dialog,
  DialogContent,
  DialogContentText,
  Menu,
  RadioGroup,
  FormControl,
  FormControlLabel,
  Radio,
  TableContainer,
  TableRow,
  TableHead,
  TableCell,
  Table,
  TableBody,
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import SortIcon from "@material-ui/icons/Sort";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import { BrowserRouter, Redirect } from "react-router-dom";
import useUserFrom from "./useUsersForm";
import { makeStyles } from "@material-ui/core/styles";
import variables from "../../variables.module.scss";
import "../../assets/style/styles.scss";

const useStyles = makeStyles({
  header: {
    fontWeight: variables.fontwt700,
    fontFamily: variables.sfProDisplay,
    fontSize: variables.fontSize34,
    color: variables.darkColor,
  },
  tableRow: {
    height: 20,
  },
  tableCell: {
    padding: "0px 16px",
  },
  tableCellName: {
    color: variables.secondaryColor,
    fontSize: variables.fontSize15,
    fontWeight: variables.fontwt700,
    fontFamily: variables.sfProText,
  },
  addBtn: {
    textTransform: "none",
    color: variables.whiteColor,
    backgroundColor: variables.tertiaryColor,
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
  },
  tableHead: {
    fontWeight: variables.fontwt700,
    color: variables.lightBlackColor,
    fontSize: variables.fontSize12,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  sortByBtn: {
    color: variables.tertiaryColor,
    fontWeight: variables.fontWeight,
    fontSize: variables.fontSize15,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  tableCellData: {
    fontSize: variables.fontSize15,
    fontWeight: variables.fontWeight,
    fontFamily: variables.sfProText,
    color: variables.dimGrayColor,
    fontStyle: "normal",
  },
  cancelBtn: {
    color: variables.neroColor,
    fontSize: variables.fontSize15,
    border: "1px solid #212121",
    fontFamily: variables.sfProText,
    borderRadius: "8px",
    width: "211px",
    height: "55px",
  },
  deleteBtn: {
    color: variables.whiteColor,
    fontSize: variables.fontSize15,
    fontFamily: variables.sfProText,
    width: "211px",
    height: "55px",
    backgroundColor: variables.tertiaryColor,
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
  },
  dialogContentText: {
    fontWeight: "bold",
    color: variables.darkColor,
    fontFamily: variables.sfProText,
    fontSize: variables.fontSize18,
    textAlign: "center",
  },
  dialogSubContentText: {
    color: variables.dimGrayColor,
    fontStyle: "normal",
    fontWeight: variables.fontwt500,
    fontSize: variables.fontSize18,
    left: "250px",
    lineHeight: "21px",
    textAlign: "center",
  },
  sortBtn: {
    color: variables.whiteColor,
    fontSize: variables.fontSize15,
    fontFamily: variables.sfProText,
    backgroundColor: variables.tertiaryColor,
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
    width: "180px",
  },
  formLabel: {
    color: variables.neroColor,
    fontWeight: variables.fontwt600,
    fontFamily: variables.sfProDisplay,
    fontSize: variables.fontSize24,
  },
  paginationIcon: {
    color: variables.secondaryColor,
    width: "17.5px",
    height: "10.81px",
  },
  paginationText: {
    color: variables.dimGrayColor,
    fontSize: variables.fontSize16,
    fontWeight: variables.fontwt700,
    fontStyle: "normal",
  },
  userGrid: {
    marginTop: "1%",
  },
  pagerGrid: {
    paddingLeft: "1%",
  },
  selectColor: {
    color: variables.secondary,
    fontFamily: variables.sfProText,
    "&$checked": {
      color: variables.secondary,
    },
  },
  checked: {},
  widthPct30: {
    width: "30%",
  },
  widthPct25: {
    width: "25%",
  },
  widthPct15: {
    width: "15%",
  },
});
const paperMenu = {
  width: 220,
  borderRadius: 10,
};

const Users = () => {
  const {
    currentDate,
    toOpen,
    toAdd,
    values,
    toOpenSortby,
    data,
    handleAddUser,
    handleClickEdit,
    handleClickOpen,
    handleClose,
    handleOpenSortby,
    handleCloseSortby,
    sortOption,
    handleSortOptionChange,
  } = useUserFrom();

  const classes = useStyles();
  return (
    <>
      <Container maxWidth="lg">
        {toAdd || values.toEdit ? <Redirect to="/AddUser" /> : null}
        <Grid container className="pt1pct">
          <Button variant="outlined" className="btn-small">
            <SearchIcon className="secondary-color"></SearchIcon>
          </Button>
        </Grid>
        <Grid container className="pt1pct">
          <Grid item>
            <Typography className="date-text">{currentDate}</Typography>
          </Grid>
        </Grid>
        <Grid container className="pt1pct">
          <Grid item>
            <Typography className={classes.header}>Users</Typography>
          </Grid>
        </Grid>
        <Grid container justify="flex-end">
          <Grid item>
            <Button
              variant="contained"
              className={classes.addBtn}
              onClick={handleAddUser}
            >
              Add New
            </Button>
          </Grid>
        </Grid>
        {/* User Details */}
        <Grid className="pt1pct">
          <TableContainer component={Paper} className="table-container">
            <Table>
              <colgroup>
                <col className={classes.widthPct30} />
                <col className={classes.widthPct30} />
                <col className={classes.widthPct25} />
                <col className={classes.widthPct15} />
              </colgroup>
              <TableHead className="table-headerBorder">
                <TableRow className={classes.tableRow}>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>NAME</Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>EMAIL</Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>ROLE</Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <IconButton onClick={handleOpenSortby}>
                      <SortIcon className="tertiary-color"></SortIcon>
                      <Typography className={classes.sortByBtn}>
                        Sort by
                      </Typography>
                    </IconButton>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {data.usersList.map((item) => (
                  <TableRow key={item.id} className={classes.tableRow}>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellName}>
                        {item.first_name+" "+item.last_name}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellData}>
                        {item.email}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellData}>
                        {item.type}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <IconButton onClick={() => handleClickEdit(item.id)}>
                        <EditIcon className="gray-color" />
                      </IconButton>
                      <IconButton onClick={handleClickOpen}>
                        <DeleteIcon className="gray-color" />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        {/*Pagination*/}
        <Grid className="pt1pct">
          <Grid container justify="flex-end" alignItems="center" spacing={1}>
            <Grid item>
              <Typography className={classes.paginationText}>
                Showing page
              </Typography>
            </Grid>
            <Grid item>
              <ArrowBackIosIcon
                className={classes.paginationIcon}
              ></ArrowBackIosIcon>
            </Grid>
            <Grid item>
              <Typography className={classes.paginationText}>1</Typography>
            </Grid>
            <Grid item>
              <Typography className={classes.paginationText}>of</Typography>
            </Grid>
            <Grid item>
              <Typography className={classes.paginationText}>3</Typography>
            </Grid>
            <Grid item>
              <ArrowForwardIosIcon
                className={classes.paginationIcon}
              ></ArrowForwardIosIcon>
            </Grid>
          </Grid>
        </Grid>
        {/* Delete popup */}
        <Grid>
          <Dialog
            maxWidth={"md"}
            modal={false}
            open={toOpen}
            onRequestClose={handleClose}
            className="dialog-container"
          >
            <DialogContent>
              <DialogContentText>
                <Typography className="dialog-content-text">
                  Are you sure you want to delete this user?
                </Typography>
                <Typography className={classes.dialogSubContentText}>
                  This cannot be undone
                </Typography>
              </DialogContentText>
              <Grid container justify="center" spacing={2}>
                <Grid item>
                  <Button className="btn-cancel" onClick={handleClose}>
                    CANCEL
                  </Button>
                </Grid>
                <Grid item>
                  <Button className="btn-delete">DELETE</Button>
                </Grid>
              </Grid>
            </DialogContent>
          </Dialog>
        </Grid>
        {/* Sortby */}
        <Grid>
          <Menu
            className="userMenuDropdown"
            id="simple-menu"
            anchorEl={toOpenSortby}
            keepMounted
            open={Boolean(toOpenSortby)}
            PaperProps={{ style: paperMenu }}
          >
            <Grid container justify="center">
              <FormControl component="fieldset">
                <Typography className={classes.formLabel}>Sort by</Typography>
                <RadioGroup
                  aria-label="Sortby"
                  name="sort"
                  value={sortOption}
                  onChange={handleSortOptionChange}
                >
                  <FormControlLabel
                    value="FirstName"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="First Name"
                  />
                  <FormControlLabel
                    value="Last Name"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Last Name"
                  />
                  <FormControlLabel
                    value="Role"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Role"
                  />
                  <FormControlLabel
                    value="Email"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Email"
                  />
                </RadioGroup>
              </FormControl>
            </Grid>
            <Grid container justify="center">
              <Grid item>
                <Button className={classes.sortBtn} onClick={handleCloseSortby}>
                  SORT
                </Button>
              </Grid>
            </Grid>
          </Menu>
        </Grid>
      </Container>
    </>
  );
};

export default Users;
