import React from "react";
import {
  Button,
  Grid,
  Typography,
  IconButton,
  Container,
  Paper,
  TableContainer,
  TableRow,
  FormControl,
  FormControlLabel,
  Radio,
  Menu,
  RadioGroup,
  TableHead,
  TableCell,
  Table,
  TableBody,
  Dialog,
  DialogContent,
  DialogTitle,
  DialogContentText,
  FormLabel,
} from "@material-ui/core";
import SortIcon from "@material-ui/icons/Sort";
import DeleteIcon from "@material-ui/icons/Delete";
import { Redirect } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import useCustomerSupportRequestForm from "./useCustomerSupportRequestForm";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import variables from "../../variables.module.scss";
import "../../assets/style/styles.scss";

const useStyles = makeStyles({
  header: {
    fontWeight: variables.fontwt700,
    fontSize: variables.fontSize34,
    color: variables.darkColor,
    fontFamily: variables.sfProDisplay,
    fontStyle: "normal",
  },
  margin: {
    paddingTop: "3%",
  },
  tableRow: {
    height: 20,
  },
  tableCell: {
    padding: "0px 16px",
  },
  backBtn: {
    borderRadius: "8px",
    "&.MuiButton-root": {
      maxWidth: "40px",
      minWidth: "40px",
      maxHeight: "40px",
      minHeight: "40px",
    },
  },
  backIcon: {
    color: variables.tertiaryColor,
    width: "16px",
    height: "16px",
  },
  tableContainer: {
    width: "100%",
    borderRadius: "16px",
  },
  tableHeaderBorder: {
    borderBottom: "4px solid #EBEBEB",
  },
  tableHead: {
    fontWeight: variables.fontwt700,
    color: variables.lightBlackColor,
    fontSize: variables.fontSize12,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  sortByBtn: {
    color: variables.tertiaryColor,
    fontWeight: variables.fontWeight,
    fontSize: variables.fontSize12,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  sortByIcon: {
    color: variables.tertiaryColor,
  },
  tableCellData: {
    fontSize: variables.fontSize15,
    fontWeight: variables.fontWeight,
    color: variables.dimGrayColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  userInfo: {
    fontSize: variables.fontSize18,
    color: variables.grayColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  customerReqBtn: {
    color: variables.tertiaryColor,
    fontSize: variables.fontSize18,
    fontWeight: variables.fontwt700,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  previewText: {
    fontSize: variables.fontSize15,
    color: variables.grayColor,
    fontWeight: variables.fontWeight,
    fontFamily: variables.sfProText,
    fontStyle: "italic",
    overflow: "hidden",
    maxWidth: "212px",
    overflow: "hidden",
    "&&:before": {
      content: '"..."',
      position: "absolute",
    },
  },
  nameText: {
    color: variables.secondaryColor,
    fontSize: variables.fontSize15,
    fontWeight: variables.fontwt700,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  deleteIcon: {
    color: variables.grayColor,
  },
  inProgress: {
    color: variables.redColor,
    fontSize: variables.fontSize15,
    fontWeight: variables.fontWeight,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  resolved: {
    color: variables.greenColor,
    fontSize: variables.fontSize15,
    fontWeight: variables.fontWeight,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  contactBtn: {
    color: variables.NeroColor,
    fontSize: variables.fontSize15,
    width: "422px",
    border: "1px solid #212121",
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  resolvedBtn: {
    color: variables.whiteColor,
    fontSize: variables.fontSize15,
    width: "422px",
    backgroundColor: variables.tertiaryColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
  },
  dialogHeader: {
    fontSize: variables.fontSize12,
    fontWeight: variables.fontwt700,
    color: variables.lightBlackColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  dialogSubHeader: {
    fontSize: variables.fontSize15,
    fontWeight: variables.fontwt700,
    color: variables.secondaryColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  radioBtn: {
    color: variables.secondaryColor,
    "&$checked": {
      color: variables.secondaryColor,
    },
  },
  checked: {},
  dialogContent: {
    fontWeight: variables.fontWeight,
    fontSize: variables.fontSize15,
    color: variables.blackColor,
    width: "921px",
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  sortBtn: {
    width: "191px",
    backgroundColor: variables.tertiaryColor,
    borderRadius: "8px",
    fontWeight: variables.fontWeight,
    fontSize: variables.fontSize15,
    color: variables.whiteColor,
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  radioBtnLabel: {
    color: variables.neroColor,
    fontWeight: variables.fontwt700,
    fontSize: variables.fontSize28,
    fontFamily: variables.sfProDisplay,
    fontStyle: "normal",
  },
});

const paperMenu = {
  width: 200,
  borderRadius: 10,
  padding: "8px",
};

const CustomerSupportRequestForm = () => {
  const {
    data,
    toOpenSortBy,
    handleClickOpen,
    handleOpenSortBy,
    toOpen,
    handleClose,
    sortOption,
    handleSortOptionChange,
    handleCloseSortBy,
    handleBack,
    toBack,
  } = useCustomerSupportRequestForm();

  const classes = useStyles();

  return (
    <>
      {toBack ? <Redirect to="/AddNurseForm" /> : null}
      <Container maxWidth="lg">
        <Grid container className={classes.margin}>
          <Button
            variant="outlined"
            className={classes.backBtn}
            onClick={handleBack}
          >
            <ArrowBackIcon className={classes.backIcon}></ArrowBackIcon>
          </Button>
        </Grid>
        <Grid container lassName={classes.margin}>
          <Grid item sm={6}>
            <Grid item className="pt1pct">
              <Typography className={classes.header}>
                Customer Support Requests
              </Typography>
            </Grid>
          </Grid>
          <Grid item sm={6}>
            <Grid container spacing={2} alignItems="center" justify="flex-end">
              <Grid item>
                <Typography className={classes.userInfo}>User Info</Typography>
              </Grid>
              <Grid item>
                <Typography className={classes.customerReqBtn}>
                  Customer Support Requests
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        {/* Customer Request Details */}
        <Grid className="pt1pct">
          <TableContainer component={Paper} className="table-container">
            <Table>
              <TableHead className="table-headerBorder">
                <TableRow className={classes.tableRow}>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>NAME</Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>DATE</Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>
                      PREVIEW
                    </Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>
                      STATUS
                    </Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <IconButton onClick={handleOpenSortBy}>
                      <SortIcon className="tertiary-color"></SortIcon>
                      <Typography className={classes.sortByBtn}>
                        Sort by
                      </Typography>
                    </IconButton>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {data.requestDetails.map((item) => (
                  <TableRow
                    key={item.id}
                    className={classes.tableRow}
                    onClick={() =>
                      handleClickOpen(item.Preview, item.date, item.name)
                    }
                  >
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.nameText}>
                        {item.name}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellData}>
                        {item.date}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <Typography noWrap className={classes.previewText}>
                        {item.Preview}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      {item.Status === "Inprogress" ? (
                        <Typography className={classes.inProgress}>
                          {item.Status}
                        </Typography>
                      ) : (
                        <Typography className={classes.resolved}>
                          {item.Status}
                        </Typography>
                      )}
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <IconButton>
                        <DeleteIcon className={classes.deleteIcon} />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        {/* Request popup */}
        <Grid>
          <Dialog
            maxWidth={"md"}
            modal={false}
            open={toOpen.open}
            onRequestClose={handleClose}
          >
            <DialogTitle id="customized-dialog-title">
              <Typography className={classes.dialogHeader}>
                {toOpen.date}
              </Typography>
            </DialogTitle>
            <DialogContent dividers>
              <Typography className={classes.dialogSubHeader}>
                {toOpen.name}
              </Typography>
              <DialogContentText className={classes.dialogContent}>
                {toOpen.previewText}
              </DialogContentText>
              <Grid container spacing={2}>
                <Grid item>
                  <Button className={classes.contactBtn} onClick={handleClose}>
                    CONTACT
                  </Button>
                </Grid>
                <Grid item>
                  <Button className={classes.resolvedBtn}>
                    MARK AS RESOLVED
                  </Button>
                </Grid>
              </Grid>
            </DialogContent>
          </Dialog>
        </Grid>
      </Container>
      {/* Sortby */}
      <Grid>
        <Menu
          className="userMenuDropdown"
          id="simple-menu"
          anchorEl={toOpenSortBy}
          keepMounted
          open={Boolean(toOpenSortBy)}
          PaperProps={{ style: paperMenu }}
        >
          <Grid container>
            <FormControl component="fieldset">
              <FormLabel className={classes.radioBtnLabel}>Sort by</FormLabel>
              <RadioGroup
                aria-label="Sortby"
                name="sort"
                value={sortOption}
                onChange={handleSortOptionChange}
              >
                <FormControlLabel
                  value="firstname"
                  control={
                    <Radio
                      disableRipple
                      classes={{
                        root: classes.radioBtn,
                        checked: classes.checked,
                      }}
                    />
                  }
                  label="Name"
                />
                <FormControlLabel
                  value="lastname"
                  control={
                    <Radio
                      disableRipple
                      classes={{
                        root: classes.radioBtn,
                        checked: classes.checked,
                      }}
                    />
                  }
                  label="Date"
                />
                <FormControlLabel
                  value="certifications"
                  control={
                    <Radio
                      disableRipple
                      classes={{
                        root: classes.radioBtn,
                        checked: classes.checked,
                      }}
                    />
                  }
                  label="Status"
                />
              </RadioGroup>
            </FormControl>
          </Grid>

          <Grid container justify="center"></Grid>
          <Grid container justify="center">
            <Grid item>
              <Button
                variant="contained"
                className={classes.sortBtn}
                onClick={handleCloseSortBy}
              >
                SORT
              </Button>
            </Grid>
          </Grid>
        </Menu>
      </Grid>
    </>
  );
};

export default CustomerSupportRequestForm;
