import React, { useState, useEffect } from "react";
import { useFormik } from "formik";
import ApiGet from "../Api/Get";
import ApiPost from "../Api/Post";

const tabValue = 3;
const useEditHospitalForm = (callback) => {
  const [buttonId, setbuttonId] = useState(0);
  const [primaryColor, setPrimaryColor] = useState({
    primaryColor: "",
    secondaryColor: "",
    tertiaryColor: "",
  });
  const [toOpenDialog, setToOpenDialog] = useState(false);
  const [adminData, setData] = useState([]);
  const [selectedAdmin, setSelectedAdmin] = useState(0);
  const [adminName, setAdminName] = useState({
    adminName: [],
  });
  const [selectedAdmins, setSelectedAdmins] = useState([]);
  const formik = useFormik({
    initialValues: {
      name: "",
      address: "",
      city: "",
      state: "",
      zipcode: "",
    },
    onSubmit: async (values) => {
      debugger;
      const id = sessionStorage.getItem("hospitalId");
      const brandColor =
        primaryColor.primaryColor +
        "," +
        primaryColor.secondaryColor +
        "," +
        primaryColor.tertiaryColor;
      const response = await fetch(ApiPost.updateHospital + "/" + id, {
        method: "PUT",
        headers: {
          "content-type": "application/json",
          Authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
        body: JSON.stringify({
          name: values.name,
          address: values.address,
          city: values.city,
          state: values.state,
          zipcode: values.zipcode,
          brand_colors: brandColor,
          admins: selectedAdmins,
        }),
      });
      const responseJson = await response.json();
      if (responseJson.code === "HOSPITAL_UPDATED") {
        setTimeout(() => {
          setToBack(true);
        }, 100);
      }
    },
    onReset: (values) => {},
  });
  useEffect(() => {
    getHospital();
    getAdmin();
  }, []);
  async function getHospital() {
    let id = sessionStorage.getItem("hospitalId");
    let response = await fetch(ApiGet.getHospitals + "/" + id, {
      headers: {
        "content-type": "application/json",
        authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
    });
    let responseJson = await response.json();
    formik.setFieldValue("name", responseJson.data.name);
    formik.setFieldValue("address", responseJson.data.address);
    formik.setFieldValue("city", responseJson.data.city);
    formik.setFieldValue("state", responseJson.data.state);
    formik.setFieldValue("zipcode", responseJson.data.zipcode);
    setData(responseJson.data.admins);
    var selectedAdminList = responseJson.data.admins;
    var ids = selectedAdminList.map((item) => item.id);
    setSelectedAdmins(ids);
    var brand_colors = responseJson.data.brand_colors.split(",");
    var primaryColor = brand_colors[0];
    var secondaryColor = brand_colors[1];
    var tertiaryColor = brand_colors[2];
    var noOfColors = brand_colors.length;
    setbuttonId(noOfColors);
    setPrimaryColor({
      primaryColor: primaryColor,
      secondaryColor: secondaryColor,
      tertiaryColor: tertiaryColor,
    });
  }
  async function getAdmin() {
    debugger;
    let response = await fetch(ApiGet.getAdmin, {
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
    });
    let responseJson = await response.json();
    setAdminName({ adminName: responseJson.data });
  }
  const [toDelete, setToDelete] = useState(false);
  const [toInactive, setToInactive] = useState(false);
  const [toEdit, setToEdit] = useState(false);
  const [toBack, setToBack] = useState(false);
  const [value, setValue] = useState(tabValue);
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const handleBackbtn = () => {
    setToBack(true);
  };
  //delete hospital
  const handleCloseDelete = () => {
    setToDelete(false);
  };
  const handleOpenDelete = () => {
    setToDelete(true);
  };
  const handleOpenInactive = () => {
    setToInactive(true);
  };

  const handleCloseInactive = () => {
    setToInactive(false);
  };
  // To display the list of admins
  const handleAddAdmin = () => {
    var admin = adminName.adminName.find((item) => {
      return item.id === selectedAdmin;
    });
    setData((adminDetails) => [...adminDetails, admin]);
    setSelectedAdmins((id) => [...id, selectedAdmin]);
  };
  const handleAdminChange = (event, value) => {
    if (value !== null) {
      setSelectedAdmin(value.id);
    }
  };
  const handleDeleteAdmin = () => {
    setData(adminData.filter((item) => item.id !== selectedAdmin));
    setSelectedAdmins(selectedAdmins.filter((item) => item !== selectedAdmin));
    handleClose();
  };
  const handleDialogClickOpen = (id) => {
    setSelectedAdmin(id);
    setToOpenDialog(true);
  };
  const handleClose = () => {
    setToOpenDialog(false);
  };
  return {
    formik,
    toBack,
    toDelete,
    toInactive,
    toEdit,
    handleCloseDelete,
    handleOpenDelete,
    handleOpenInactive,
    handleCloseInactive,
    handleBackbtn,
    handleChange,
    value,
    buttonId,
    primaryColor,
    handleAddAdmin,
    handleAdminChange,
    handleDeleteAdmin,
    adminData,
    adminName,
    handleDialogClickOpen,
    handleClose,
    toOpenDialog,
  };
};

export default useEditHospitalForm;
