import React from "react";
import {
  Button,
  Grid,
  Typography,
  IconButton,
  Container,
  Paper,
  Dialog,
  DialogContent,
  DialogContentText,
  Divider,
  Menu,
  RadioGroup,
  FormControl,
  FormControlLabel,
  Radio,
  FormGroup,
  Checkbox,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Tab,
  Tabs,
} from "@material-ui/core";
import { Redirect } from "react-router-dom";
import SearchIcon from "@material-ui/icons/Search";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import SortIcon from "@material-ui/icons/Sort";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import { makeStyles } from "@material-ui/core/styles";
import { Link } from "react-router-dom";
import useNurseForm from "./useNurseForm";
import variables from "../../variables.module.scss";
import "../../assets/style/styles.scss";
const useStyles = makeStyles({
  header: {
    fontWeight: variables.fontwt700,
    fontFamily: variables.sfProDisplay,
    fontSize: variables.fontSize34,
    color: variables.darkColor,
  },
  subHeader: {
    fontWeight: variables.fontwt500,
    fontSize: variables.fontSize15,
    fontFamily: variables.sfProText,
    color: variables.dimGrayColor,
  },
  tab: {
    width: "25%",
  },

  tableRow: {
    height: 30,
  },
  tableCell: {
    padding: "0px 15px",
    fontSize: variables.fontSize15,
  },
  tableCellname: {
    color: variables.secondaryColor,
    fontFamily: variables.sfProText,
    fontSize: variables.fontSize15,
    fontWeight: variables.fontwt600,
  },
  addBtn: {
    "& .MuiButton-label": {
      width: "120px",
      height: "35px",
      backgroundColor: variables.tertiaryColor,
      borderRadius: "4px",
      color: "white",
    },
    "&.MuiButton-root:hover": {
      backgroundColor: "transparent",
    },
  },
  textColorInherit: {
    opacity: 1,
  },
  searchIcon: {
    color: variables.secondaryColor,
  },
  tableContainer: {
    width: "100%",
    fontFamily: variables.sfProDisplay,
    borderRadius: "16px",
  },
  tableHeaderBorder: {
    borderBottom: "4px solid #EBEBEB",
  },
  tableHead: {
    fontWeight: variables.fontwt700,
    color: variables.lightBlackColor,
    fontSize: variables.fontSize12,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  sortHd: {
    color: variables.tertiaryColor,
    fontWeight: variables.fontwt500,
    fontFamily: variables.sfProText,
    fontSize: variables.fontSize12,
  },
  tableCellData: {
    fontSize: variables.fontSize15,
    fontWeight: variables.fontWeight,
    fontFamily: variables.sfProText,
    color: variables.dimGrayColor,
    fontStyle: "normal",
  },
  cancelBtn: {
    color: variables.neroColor,
    fontSize: variables.fontSize15,
    fontFamily: variables.sfProText,
    border: "1px solid #212121",
    width: "211px",
    height: "55px",
  },
  deleteBtn: {
    color: variables.whiteColor,
    fontSize: variables.fontSize15,
    fontFamily: variables.sfProText,
    width: "211px",
    height: "55px",
    backgroundColor: variables.tertiaryColor,
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
  },
  dialogContentText: {
    fontWeight: "bold",
    color: variables.darkColor,
    fontFamily: variables.sfProText,
    fontSize: variables.fontSize18,
    textAlign: "center",
  },
  dialogSubText: {
    color: variables.dimGrayColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
    fontWeight: variables.fontwt500,
    fontSize: variables.fontSize18,
    left: "250px",
    lineHeight: "21px",
    textAlign: "center",
  },
  sortBtn: {
    color: variables.whiteColor,
    fontSize: variables.fontSize15,
    backgroundColor: variables.tertiaryColor,
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
    width: "180px",
  },
  formLabel: {
    color: variables.darkColor,
    fontSize: "1.5rem",
  },
  paginationIcon: {
    color: variables.secondaryColor,
    width: "17.5px",
    height: "10.81px",
  },
  paginationText: {
    fontFamily: variables.sfProText,
    color: variables.dimGrayColor,
    fontSize: "16px",
    fontWeight: "normal",
  },
  searchBtn: {
    borderRadius: variables.borderRadius,
    "&.MuiButton-root": {
      maxWidth: "40px",
      minWidth: "40px",
      maxHeight: "40px",
      minHeight: "40px",
    },
  },
  exportBtn: {
    color: variables.dimGrayColor,
    fontSize: variables.fontSize16,
    fontStyle: "normal",
    fontWeight: "normal",
  },
  export: {
    color: variables.whiteColor,
    fontFamily: variables.sfProText,
    fontSize: variables.fontSize15,
    width: "293px",
    height: "55px",
    backgroundColor: variables.tertiaryColor,
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
    fontWeight: variables.fontwt500,
    textAlign: "center",
  },
  exportCancel: {
    color: variables.darkColor,
    fontFamily: variables.sfProText,
    fontSize: variables.fontSize15,
    width: "293px",
    height: "55px",
    borderRadius: "8px",
    border: "1px solid #212121",
    backgroundColor: variables.whiteColor,
    "&:hover": {
      backgroundColor: variables.whiteColor,
    },
    fontWeight: variables.fontwt500,
    textAlign: "center",
  },
  exportLabel: {
    fontSize: variables.fontSize28,
    fontFamily: variables.sfProText,
    fontWeight: variables.fontwt600,
    color: variables.darkColor,
    paddingLeft: "5px",
  },
  sortIcon: {
    color: variables.tertiaryColor,
  },
  grid: {
    paddingTop: "2%",
  },
  tableGrid: {
    paddingTop: "1%",
  },
  icon: {
    color: variables.grayColor,
  },
  pagerGrid: {
    paddingLeft: "1%",
  },
  indicator: {
    height: "4px",
    backgroundColor: variables.tertiaryColor,
  },
  tabMainContainer: {
    marginTop: "1%",
    width: "100%",
  },
  tabsContainer: {
    fontFamily: variables.sfProText,
    width: "100%",
    borderBottom: "4px solid #D6D6D6",
  },
  firstName: {
    marginLeft: "20px",
  },
  lastName: {
    marginTop: "10%",
  },
  selectColor: {
    color: variables.secondaryColor,
    fontFamily: variables.sfProText,
    "&$checked": {
      color: variables.secondaryColor,
    },
  },
  checked: {},
});
const sortMenu = {
  width: "220",
  borderRadius: "10",
  padding: "20px",
};
const exportMenu = {
  width: "357px",
  height: "400px",
  borderRadius: "10",
};

const Nurses = () => {
  const {
    data,
    toOpen,
    toAdd,
    toEdit,
    sortOption,
    toOpenSortby,
    toOpenExport,
    values,
    value,
    handleClickOpen,
    handleClickEdit,
    handleClose,
    handleOpenSortby,
    handleCloseSortby,
    handleCloseExport,
    handleOpenExport,
    handleAddNurse,
    handleSortOptionChange,
    handleChange,
    handleTabChange,
  } = useNurseForm();

  const classes = useStyles();

  return (
    <>
      <Container maxWidth="lg">
        {toAdd || toEdit ? <Redirect to="/AddNurseForm" /> : null}
        <Grid container className="pt1pct">
          <Button variant="outlined" className={classes.searchBtn}>
            <SearchIcon className={classes.searchIcon}></SearchIcon>
          </Button>
        </Grid>
        <Grid container className="pt1pct">
          <Grid item>
            <Typography className={classes.subHeader}>Staff</Typography>
          </Grid>
          <Grid container justify={"flex-start"}>
            <Typography className={classes.header}>
              {sessionStorage.getItem("hospitalName")}
            </Typography>
          </Grid>
        </Grid>

        <Grid className={classes.tabMainContainer}>
          <Tabs
            value={value}
            variant="fullWidth"
            textColor="primary"
            onChange={handleTabChange}
            classes={{ indicator: classes.indicator }}
          >
            <Tab label="UNITS" className={classes.tabsContainer} />
            <Tab label="STAFF" className={classes.tabsContainer} />
            <Tab label="PATIENTS" className={classes.tabsContainer} />
            <Tab
              label="BASIC INFORMATION"
              className={classes.tabsContainer}
              to="/Hospitals"
              component={Link}
            />

            <Tab
              label="Add New"
              component={Button}
              onClick={handleAddNurse}
              className={classes.addBtn + " " + classes.tabsContainer}
              classes={{ textColorInherit: classes.textColorInherit }}
            />
          </Tabs>
        </Grid>
        {/* Nurse Details */}
        <Grid className={classes.tableGrid}>
          <TableContainer component={Paper} className="table-container">
            <Table>
              <TableHead className="table-headerBorder">
                <TableRow className={classes.tableRow}>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>NAME</Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>TITLE</Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>EMAIL</Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>
                      PHONE NUMBER
                    </Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>
                      PERMISSIONS
                    </Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <IconButton onClick={handleOpenSortby}>
                      <SortIcon className="tertiary-color"></SortIcon>
                      <Typography className={classes.sortHd}>
                        Sort by
                      </Typography>
                    </IconButton>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {data.nurseList.map((item) => (
                  <TableRow key={item.id} className={classes.tableRow}>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellname}>
                        {item.user.first_name + " " + item.user.last_name}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellData}>
                        {item.title}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellData}>
                        {item.user.email}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellData}>
                        {item.user.phone}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellData}>
                        {item.roles}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <IconButton onClick={() => handleClickEdit(item.id)}>
                        <EditIcon className="gray-color" />
                      </IconButton>
                      <IconButton onClick={() => handleClickOpen(item.id)}>
                        <DeleteIcon className="gray-color" />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        {/*Details end*/}
        <Grid className={classes.grid}>
          <Grid container>
            <Grid item sm={6} className={classes.pagerGrid}>
              <Button onClick={handleOpenExport} className={classes.exportBtn}>
                Export .CSV
              </Button>
            </Grid>
            <Grid item sm={6}>
              <Grid
                container
                justify="flex-end"
                spacing={1}
                alignItems="center"
              >
                <Grid item>
                  <Typography className={classes.paginationText}>
                    Showing page
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography className={classes.paginationText}>1</Typography>
                </Grid>
                <Grid item>
                  <Typography className={classes.paginationText}>of</Typography>
                </Grid>
                <Grid item>
                  <Typography className={classes.paginationText}>3</Typography>
                </Grid>
                <Grid>
                  <ArrowForwardIosIcon
                    className={classes.paginationIcon}
                  ></ArrowForwardIosIcon>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
        {/* Delete popup */}
        <Grid>
          <Dialog
            maxWidth={"md"}
            modal={false}
            open={toOpen}
            onRequestClose={handleClose}
            className="dialog-container"
          >
            <DialogContent>
              <DialogContentText>
                <Typography className="dialog-content-text">
                  Are you sure you want to delete this staff?
                </Typography>
                <Typography className={classes.dialogSubText}>
                  This cannot be undone
                </Typography>
              </DialogContentText>
              <Grid container justify="center" spacing={2}>
                <Grid item>
                  <Button className="btn-cancel" onClick={handleClose}>
                    CANCEL
                  </Button>
                </Grid>
                <Grid item>
                  <Button className="btn-delete">DELETE</Button>
                </Grid>
              </Grid>
            </DialogContent>
          </Dialog>
        </Grid>
        {/* Sortby */}
        <Grid>
          <Menu
            className="userMenuDropdown"
            id="simple-menu"
            anchorEl={toOpenSortby}
            keepMounted
            open={Boolean(toOpenSortby)}
            PaperProps={{ style: sortMenu }}
          >
            <Grid container justify="center">
              <FormControl component="fieldset">
                <Typography className={classes.formLabel}>Sort by</Typography>
                <RadioGroup
                  aria-label="Sortby"
                  name="sort"
                  value={sortOption}
                  onChange={handleSortOptionChange}
                >
                  <FormControlLabel
                    value="firstname"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="First Name"
                  />
                  <FormControlLabel
                    value="lastname"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Last Name"
                  />
                  <FormControlLabel
                    value="certifications"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Certifications"
                  />
                  <FormControlLabel
                    value="credentials"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Credentials"
                  />
                  <FormControlLabel
                    value="title"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Title"
                  />
                  <FormControlLabel
                    value="startdate"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Start Date"
                  />
                  <FormControlLabel
                    value="yearofexperience"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Years of experience"
                  />
                  <FormControlLabel
                    value="email"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Email"
                  />
                </RadioGroup>
              </FormControl>
            </Grid>
            <Grid container justify="center">
              <Grid item>
                <Button className={classes.sortBtn} onClick={handleCloseSortby}>
                  SORT
                </Button>
              </Grid>
            </Grid>
          </Menu>
        </Grid>
        {/*Export*/}
        <Grid>
          <Menu
            className="userMenuDropdown"
            id="simple-menu"
            anchorEl={toOpenExport}
            keepMounted
            open={Boolean(toOpenExport)}
            PaperProps={{ style: exportMenu }}
          >
            <Grid container justify="flex-start">
              <FormControl component="fieldset">
                <Typography className={classes.exportLabel}>
                  Export .CSV
                </Typography>
                <FormGroup className={classes.firstName}>
                  <FormControlLabel
                    control={
                      <Checkbox
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                        name="fname"
                        checked={values.fname}
                        onChange={handleChange}
                      />
                    }
                    label="First Name"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                        name="certifications"
                        checked={values.certifications}
                        onChange={handleChange}
                      />
                    }
                    label="Certifications"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                        name="credentials"
                        checked={values.credentials}
                        onChange={handleChange}
                      />
                    }
                    label="Credentials"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                        name="startDate"
                        checked={values.startDate}
                        onChange={handleChange}
                      />
                    }
                    label="Start date"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                        name="email"
                        checked={values.email}
                        onChange={handleChange}
                      />
                    }
                    label="Email"
                  />
                </FormGroup>
              </FormControl>
              <FormControl component="fieldset" className={classes.lastName}>
                <FormGroup>
                  <FormControlLabel
                    control={
                      <Checkbox
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                        name="lname"
                        checked={values.lname}
                        onChange={handleChange}
                      />
                    }
                    label="Last name"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                        name="hospital"
                        checked={values.hospital}
                        onChange={handleChange}
                      />
                    }
                    label="Hospital/Location"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                        name="title"
                        checked={values.title}
                        onChange={handleChange}
                      />
                    }
                    label="Title"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        classes={{
                          root: classes.selectColor,
                          checked: classes.checked,
                        }}
                        name="experience"
                        checked={values.experience}
                        onChange={handleChange}
                      />
                    }
                    label="Experience"
                  />
                </FormGroup>
              </FormControl>
            </Grid>
            <Grid container justify="center">
              <Grid item>
                <Button className={classes.export} onClick={handleCloseExport}>
                  EXPORT
                </Button>
              </Grid>
              <Grid className={classes.grid}>
                <Button
                  className={classes.exportCancel}
                  onClick={handleCloseExport}
                >
                  CANCEL
                </Button>
              </Grid>
            </Grid>
          </Menu>
        </Grid>
      </Container>
    </>
  );
};

export default Nurses;
