import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Login from "../components/Login/LoginForm";
import Hospitals from "./Layout";
import Users from "./Layout";
import AddHospital from "./Layout";
import AddUser from "./Layout";
import EditHospital from "./Layout";
import Nurses from "./Layout";
import AddNurseForm from "./Layout";
import CustomerSupportRequestForm from "./Layout";
import AddUnitForm from "./Layout";
import EditUnitForm from "./Layout";
const Routes = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route exact path="/" component={Login} />
        <Route exact path="/Login" component={Login} />
        <Route path="/Hospitals" component={Hospitals} />
        <Route path="/Users" component={Users} />
        <Route path="/AddHospital" component={AddHospital} />
        <Route path="/EditHospital" component={EditHospital} />
        <Route path="/Nurses" component={Nurses} />
        <Route path="/AddNurseForm" component={AddNurseForm} />
        <Route path="/AddUnitForm" component={AddUnitForm} />
        <Route path="/EditUnitForm" component={EditUnitForm} />
        <Route
          path="/CustomerSupportRequestForm"
          component={CustomerSupportRequestForm}
        />
        <Route path="/AddUser" component={AddUser} />
      </Switch>
    </BrowserRouter>
  );
};

export default Routes;
