import React from "react";
import {
  Button,
  Grid,
  Typography,
  IconButton,
  Container,
  Paper,
  Dialog,
  DialogContent,
  DialogContentText,
  Menu,
  RadioGroup,
  FormControl,
  FormControlLabel,
  Radio,
  FormLabel,
  TableContainer,
  TableRow,
  TableHead,
  TableCell,
  Table,
  TableBody,
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import SortIcon from "@material-ui/icons/Sort";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import { Redirect } from "react-router-dom";
import useHospitalForm from "./useHospitalList";
import { makeStyles } from "@material-ui/core/styles";
import variables from "../../variables.module.scss";
import "../../assets/style/styles.scss";

const HospitalList = () => {
  const {
    currentDate,
    handleAddHospital,
    toAdd,
    data,
    toOpenSortBy,
    handleClickOpen,
    handleOpenSortBy,
    toOpen,
    handleClose,
    sortOption,
    handleSortOptionChange,
    handleCloseSortBy,
    handleEdit,
    toEdit,
    handleDelete
  } = useHospitalForm();
  const useStyles = makeStyles({
    header: {
      fontWeight: variables.fontwt700,
      fontSize: variables.fontSize34,
      color: variables.darkColor,
      fontFamily: variables.sfProText,
    },
    tableRow: {
      height: 20,
    },
    tableCell: {
      padding: "0px 16px",
    },
    addNewBtn: {
      textTransform: "none",
      color: variables.whiteColor,
      backgroundColor: variables.tertiaryColor,
      fontFamily: variables.sfProText,
      fontWeight: variables.fontWeight,
      "&:hover": {
        backgroundColor: variables.tertiaryColor,
      },
    },
    tableHead: {
      fontWeight: variables.fontwt700,
      color: variables.lightBlackColor,
      fontSize: variables.fontSize12,
      fontFamily: variables.sfProText,
      fontStyle: "normal",
    },
    sortByBtn: {
      color: variables.tertiaryColor,
      fontWeight: variables.fontWeight,
      fontSize: variables.fontSize15,
      fontFamily: variables.sfProText,
      fontStyle: "normal",
    },
    tableCellData: {
      fontSize: variables.fontSize15,
      fontWeight: variables.fontWeight,
      fontFamily: variables.sfProText,
      color: variables.dimGrayColor,
      fontStyle: "normal",
    },
    dialogContentSubHeader: {
      color: variables.dimGrayColor,
      fontFamily: variables.sfProText,
      fontStyle: "normal",
    },
    radioBtn: {
      color: variables.secondaryColor,
      "&$checked": {
        color: variables.secondaryColor,
      },
    },
    checked: {},
    sortBtn: {
      color: variables.whiteColor,
      fontSize: variables.fontSize15,
      backgroundColor: variables.tertiaryColor,
      fontFamily: variables.sfProText,
      fontStyle: "normal",
      fontFamily: variables.fontWeight,
      width: "180px",
      "&:hover": {
        backgroundColor: variables.tertiaryColor,
      },
    },
    resetBtn: {
      color: variables.secondaryColor,
      textTransform: "none",
      fontFamily: variables.sfProText,
      fontStyle: "normal",
      fontWeight: variables.fontWeight,
    },
    formLabel: {
      color: variables.neroColor,
      fontSize: "1.5rem",
      fontFamily: variables.sfProDisplay,
      fontStyle: "normal",
    },
    paginationIcon: {
      color: variables.secondaryColor,
      width: "17.5px",
      height: "10.81px",
    },
    paginationText: {
      color: variables.dimGrayColor,
      fontSize: variables.fontSize16,
      fontWeight: variables.fontwt700,
      fontStyle: "normal",
    },
    iconColor: {
      color: variables.grayColor,
    },
    hospitalNameText: {
      color: variables.secondaryColor,
      fontSize: variables.fontSize15,
      fontWeight: variables.fontwt700,
      fontFamily: variables.sfProText,
    },
    widthPct30: {
      width: "30%",
    },
    widthPct25: {
      width: "25%",
    },
    widthPct15: {
      width: "15%",
    },

    root: {},
    paper: { borderRadius: 15 },
  });
  const paperMenu = {
    width: 200,
    borderRadius: 10,
    padding: "10px",
  };
  const classes = useStyles();

  return (
    <>
      {toAdd ? <Redirect to="/AddHospital" /> : null}
      {toEdit ? <Redirect to="/EditHospital" /> : null}
      <Container maxWidth="lg">
        <Grid container className="pt1pct">
          <Button variant="outlined" className="btn-small">
            <SearchIcon className="secondary-color"></SearchIcon>
          </Button>
        </Grid>
        <Grid container className="pt1pct">
          <Grid item>
            <Typography className="date-text">{currentDate}</Typography>
          </Grid>
        </Grid>
        <Grid container className="pt1pct">
          <Grid item>
            <Typography className={classes.header}>Hospitals</Typography>
          </Grid>
        </Grid>
        <Grid container justify="flex-end">
          <Grid item>
            <Button
              variant="contained"
              className={classes.addNewBtn}
              onClick={handleAddHospital}
            >
              Add New
            </Button>
          </Grid>
        </Grid>
        {/* Hospital Details */}
        <Grid className="pt1pct">
          <TableContainer component={Paper} className="table-container">
            <Table>
              <colgroup>
                <col className={classes.widthPct30} />
                <col className={classes.widthPct30} />
                <col className={classes.widthPct25} />
                <col className={classes.widthPct15} />
              </colgroup>
              <TableHead className="table-headerBorder">
                <TableRow className={classes.tableRow}>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>
                      HOSPITAL NAME
                    </Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>STATE</Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>
                      ZIPCODE
                    </Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <IconButton onClick={handleOpenSortBy}>
                      <SortIcon className="tertiary-color"></SortIcon>
                      <Typography className={classes.sortByBtn}>
                        Sort by
                      </Typography>
                    </IconButton>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {data.hospitalDetails.map((item) => (
                  <TableRow key={item.id} className={classes.tableRow}>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.hospitalNameText}>
                        {item.name}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellData}>
                        {item.state}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.tableCellData}>
                        {item.zipcode}
                      </Typography>
                    </TableCell>

                    <TableCell className={classes.tableCell}>
                      <IconButton onClick={()=>handleEdit(item.id, item.name)}>
                        <EditIcon className="gray-color" />
                      </IconButton>
                      <IconButton onClick={()=>handleClickOpen(item.id)}>
                        <DeleteIcon className="gray-color" />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Grid>
        {/*Pagination*/}
        <Grid className="pt1pct">
          <Grid container justify="flex-end" alignItems="center" spacing={1}>
            <Grid item>
              <Typography className={classes.paginationText}>
                Showing page
              </Typography>
            </Grid>
            <Grid item>
              <ArrowBackIosIcon
                className={classes.paginationIcon}
              ></ArrowBackIosIcon>
            </Grid>
            <Grid item>
              <Typography className={classes.paginationText}>1</Typography>
            </Grid>
            <Grid item>
              <Typography className={classes.paginationText}>of</Typography>
            </Grid>
            <Grid item>
              <Typography className={classes.paginationText}>3</Typography>
            </Grid>
            <Grid item>
              <ArrowForwardIosIcon
                className={classes.paginationIcon}
              ></ArrowForwardIosIcon>
            </Grid>
          </Grid>
        </Grid>
        {/* Delete popup */}
        <Grid>
          <Dialog
            maxWidth={"md"}
            modal={false}
            open={toOpen}
            onRequestClose={handleClose}
            className="dialog-container"
          >
            <DialogContent>
              <DialogContentText>
                <Typography className="dialog-content-text">
                  Are you sure want to delete this hospital?
                </Typography>
                <Typography
                  align="center"
                  className={classes.dialogContentSubHeader}
                >
                  This cannot be undone
                </Typography>
              </DialogContentText>
              <Grid container justify="center" spacing={2}>
                <Grid item>
                  <Button
                    variant="outlined"
                    className="btn-cancel"
                    onClick={handleClose}
                  >
                    CANCEL
                  </Button>
                </Grid>
                <Grid item>
                  <Button variant="outlined" className="btn-delete" onClick={handleDelete}>
                    DELETE
                  </Button>
                </Grid>
              </Grid>
            </DialogContent>
          </Dialog>
        </Grid>
        {/* Sortby */}
        <Grid>
          <Menu
            className="userMenuDropdown"
            id="simple-menu"
            anchorEl={toOpenSortBy}
            keepMounted
            open={Boolean(toOpenSortBy)}
            PaperProps={{ style: paperMenu }}
          >
            <Grid container justify="center">
              <FormControl component="fieldset">
                <FormLabel className={classes.formLabel}>Sort by</FormLabel>
                <RadioGroup
                  aria-label="Sortby"
                  name="sort"
                  value={sortOption}
                  onChange={handleSortOptionChange}
                >
                  <FormControlLabel
                    value="Name"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.radioBtn,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Name"
                  />
                  <FormControlLabel
                    value="State"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.radioBtn,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="State"
                  />
                  <FormControlLabel
                    value="zipcode"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.radioBtn,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Zip code"
                  />
                  <FormControlLabel
                    value="Activehospital"
                    control={
                      <Radio
                        disableRipple
                        classes={{
                          root: classes.radioBtn,
                          checked: classes.checked,
                        }}
                      />
                    }
                    label="Active Hospitals"
                  />
                </RadioGroup>
              </FormControl>
            </Grid>
            <Grid container justify="center" spacing={1}>
              <Grid item>
                <Button
                  variant="outlined"
                  className={classes.sortBtn}
                  onClick={handleCloseSortBy}
                >
                  SORT
                </Button>
              </Grid>
              <Grid item>
                <Button className={classes.resetBtn}>Reset</Button>
              </Grid>
            </Grid>
          </Menu>
        </Grid>
      </Container>
    </>
  );
};

export default HospitalList;
