import React from "react";
import {
  Button,
  Grid,
  Typography,
  IconButton,
  Container,
  Paper,
  Dialog,
  DialogContent,
  DialogContentText,
  Divider,
} from "@material-ui/core";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import Switch from "@material-ui/core/Switch";
import useAddNurseForm from "./useAddNurseForm";
import { makeStyles } from "@material-ui/core/styles";
import { Redirect } from "react-router-dom";
import Chip from "@material-ui/core/Chip";
import ClearOutlinedIcon from "@material-ui/icons/ClearOutlined";
import variables from "../../variables.module.scss";
import "../../assets/style/styles.scss";

const useStyles = makeStyles((theme) => ({
  userInfo: {
    color: variables.tertiaryColor,
    fontSize: variables.fontSize18,
    fontWeight: variables.fontwt700,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  customerReqBtn: {
    fontSize: variables.fontSize18,
    color: variables.grayColor,
    fontWeight: variables.fontwt700,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  header: {
    fontSize: variables.fontSize34,
    fontWeight: variables.fontwt600,
    color: variables.darkColor,
    fontFamily: variables.sfProDisplay,
    fontStyle: "normal",
  },
  basicInfo: {
    fontSize: "22px",
    fontWeight: variables.fontwt600,
    color: variables.neroColor,
    fontFamily: variables.sfProDisplay,
    fontStyle: "normal",
  },
  backIcon: {
    color: variables.tertiaryColor,
    width: "16px",
    height: "16px",
  },
  formControlMargin: {
    paddingTop: "2%",
    marginLeft: "4%",
  },
  uploadBtn: {
    width: "124px",
    height: "55px",
    border: "1px solid #212121",
    borderRadius: "8px",
    fontWeight: variables.fontWeight,
    fontSize: variables.fontSize16,
    color: variables.neroColor,
    textTransform: "none",
  },
  credentialLabel: {
    fontSize: variables.fontSize18,
    fontWeight: variables.fontWeight,
    color: variables.darkColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  unitSet: {
    height: "50px",
    borderRadius: "16px",
    width: "120px",
    margin: "1%",
    color: variables.primaryColor,
    backgroundColor: variables.whiteColor,
  },
  addUnitsBtn: {
    width: "124px",
    height: "55px",
    borderRadius: "8px",
    fontWeight: variables.fontWeight,
    fontSize: variables.fontSize16,
    color: variables.neroColor,
    border: "1px solid #212121",
    fontStyle: "normal",
  },
  popupHeader: {
    fontWeight: "bold",
    color: variables.darkColor,
    fontSize: variables.fontSize18,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  popupTextField: {
    width: "60px",
    height: "30px",
    backgroundColor: variables.lightColor,
    borderStyle: "none",
    textAlign: "center",
    color: variables.grayColor,
  },
  paper: {
    borderRadius: "16px",
  },
  pt2: {
    paddingTop: "2%",
  },
  ml3: {
    marginLeft: "3%",
  },
  notificationLabel: {
    color: variables.dimGrayColor,
    fontSize: variables.fontSize16,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  formRemoveBtn: {
    width: "211px",
    height: "55px",
    border: "1px solid #212121",
    borderRadius: "8px",
    fontWeight: variables.fontWeight,
    fontSize: variables.fontSize15,
    color: variables.neroColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  formSuspendBtn: {
    width: "211px",
    height: "55px",
    backgroundColor: variables.dimGrayColor,
    borderRadius: "8px",
    fontWeight: variables.fontWeight,
    fontSize: variables.fontSize15,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
    color: variables.whiteColor,
    "&:hover": {
      backgroundColor: variables.dimGrayColor,
    },
  },
  chargeNurseBtn: {
    backgroundColor: variables.tertiaryColor,
    borderRadius: "0px",
    height: "55px",
    width: "143.44px",
    fontWeight: variables.fontwt500,
    fontSize: variables.fontSize12,
    color: variables.whiteColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
  },
  adminBtn: {
    border: "1px solid #D6D6D6",
    borderRadius: "8px 0px 0px 8px",
    fontWeight: variables.fontwt500,
    fontSize: variables.fontSize12,
    color: variables.darkColor,
    height: "55px",
    width: "143.44px",
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  noneBtn: {
    border: "1px solid #D6D6D6",
    borderRadius: "0px 8px 8px 0px",
    height: "55px",
    width: "143.44px",
    fontWeight: variables.fontwt500,
    fontSize: variables.fontSize12,
    color: variables.darkColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  label: {
    fontWeight: variables.fontwt500,
    fontSize: variables.fontSize12,
    color: variables.darkColor,
    fontFamily: variables.sfProText,
  },
  avatarImage: {
    width: "100px",
    height: "100px",
    background: variables.silverColor,
    borderRadius: "50%",
  },
  inputTypeFile: {
    border: "0px",
    outline: "none",
    display: "none",
  },
  unitDeleteIcon: {
    color: variables.primaryColor,
  },
}));

const AddNurseForm = () => {
  const {
    formik,
    toEdit,
    handleClose,
    popup,
    handleClickSuspend,
    handleClickChargeNurse,
    toOpenSuspend,
    handleCloseSuspend,
    handleClickCustomerRequest,
    toCustomerSupportRequest,
    imageInput,
    handleImageClick,
    handleImageChange,
    image,
    unitData,
    handleDelete,
    handleAddUnits,
    showUnits,
    toBack,
    handleBack,
    handleClickChargeNurseOk,
    toRemove,
    handleOpenRemove,
    handleCloseRemove,
    handleAdmin,
    handleNone,
  } = useAddNurseForm();

  const classes = useStyles();

  return (
    <>
      {toCustomerSupportRequest ? (
        <Redirect to="/CustomerSupportRequestForm" />
      ) : null}
      {toBack ? <Redirect to="/Nurses" /> : null}
      <Container maxWidth="lg">
        <Grid container className={classes.pt2}>
          <Grid item>
            <Button
              variant="outlined"
              className="btn-small"
              onClick={handleBack}
            >
              <ArrowBackIcon className={classes.backIcon}></ArrowBackIcon>
            </Button>
          </Grid>
        </Grid>
        <Grid container className={classes.pt2} alignItems="center">
          <Grid item sm={6}>
            {toEdit === false ? (
              <Typography className={classes.header}>Add New Staff</Typography>
            ) : (
              <Typography className={classes.header}>
                User Information
              </Typography>
            )}
          </Grid>
          {toEdit === true && (
            <Grid item sm={6}>
              <Grid
                container
                spacing={2}
                alignItems="center"
                justify="flex-end"
              >
                <Grid item>
                  <Typography className={classes.userInfo}>
                    User Info
                  </Typography>
                </Grid>
                <Grid item>
                  <IconButton onClick={handleClickCustomerRequest}>
                    <Typography className={classes.customerReqBtn}>
                      Customer Support Requests
                    </Typography>
                  </IconButton>
                </Grid>
              </Grid>
            </Grid>
          )}
        </Grid>
        <Grid className={classes.pt2}>
          <Paper elevation={1} className={classes.paper}>
            <Typography className="form-title">User Information</Typography>
            <Divider className="divider" />
            <Grid container spacing={2}>
              <Grid item sm={6}>
                <Grid container className={classes.ml3}>
                  <Typography className={classes.basicInfo}>
                    Basic Information
                  </Typography>
                </Grid>
                <Grid container className={classes.formControlMargin}>
                  <Typography className={classes.label}>
                    PROFILE PHOTO
                  </Typography>
                </Grid>
                <Grid
                  container
                  alignItems="center"
                  spacing={2}
                  className={classes.ml3}
                >
                  <Grid item>
                    {image !== "" ? (
                      <img src={image} className={classes.avatarImage} />
                    ) : (
                      <img className={classes.avatarImage} />
                    )}
                  </Grid>
                  <Grid item>
                    <Button className="btn-medium" onClick={handleImageClick}>
                      Upload New
                      <input
                        type="file"
                        ref={imageInput}
                        accept="image/*"
                        className={classes.inputTypeFile}
                        onChange={handleImageChange}
                      />
                    </Button>
                  </Grid>
                </Grid>
                <Grid container spacing={2} className={classes.ml3}>
                  <Grid item>
                    <Typography className={classes.label}>
                      FIRST NAME
                    </Typography>
                    <input
                      type="text"
                      placeholder="FIRST NAME"
                      className="textInput-small"
                      name="firstName"
                      onChange={formik.handleChange}
                      value={formik.values.firstName}
                    ></input>
                  </Grid>
                  <Grid item>
                    <Typography className={classes.label}>LAST NAME</Typography>
                    <input
                      type="text"
                      placeholder="LAST NAME"
                      className="textInput-small"
                      name="lastName"
                      onChange={formik.handleChange}
                      value={formik.values.lastName}
                    ></input>
                  </Grid>
                </Grid>
                <Grid container className={classes.formControlMargin}>
                  <Grid item xs={12} sm={12}>
                    <Typography className={classes.label}>EMAIL</Typography>
                    <input
                      type="text"
                      placeholder="EMAIL"
                      className="textInput-large"
                      name="email"
                      onChange={formik.handleChange}
                      value={formik.values.email}
                    ></input>
                  </Grid>
                </Grid>
                <Grid container className={classes.formControlMargin}>
                  <Grid item xs={12} sm={12}>
                    <Typography className={classes.label}>
                      PHONE NUMBER
                    </Typography>
                    <input
                      type="text"
                      placeholder="PHONE NUMBER"
                      className="textInput-large"
                      name="phoneNumber"
                      onChange={formik.handleChange}
                      value={formik.values.phoneNumber}
                    ></input>
                  </Grid>
                </Grid>
                {toEdit === true && (
                  <Grid container alignItems="center">
                    <Grid item>
                      <Switch color="primary" />
                    </Grid>
                    <Grid item>
                      <Typography className={classes.notificationLabel}>
                        SMS Notifications
                      </Typography>
                    </Grid>
                  </Grid>
                )}
                <Grid container className={classes.formControlMargin}>
                  <Grid item>
                    <Typography className={classes.credentialLabel}>
                      Credentials
                    </Typography>
                  </Grid>
                </Grid>
                <Grid container className={classes.formControlMargin}>
                  <Grid item xs={12} sm={12}>
                    <Typography className={classes.label}>TITLE</Typography>
                    <input
                      type="text"
                      placeholder="TITLE"
                      className="textInput-large"
                      name="title"
                      onChange={formik.handleChange}
                      value={formik.values.title}
                    ></input>
                  </Grid>
                </Grid>
                <Grid container className={classes.formControlMargin}>
                  <Grid item xs={12} sm={12}>
                    <Typography className={classes.label}>
                      CERTIFICATIONS
                    </Typography>
                    <input
                      type="text"
                      placeholder="CERTIFICATIONS"
                      className="textInput-large"
                      name="certifications"
                      onChange={formik.handleChange}
                      value={formik.values.certifications}
                    ></input>
                  </Grid>
                </Grid>
                <Grid container className={classes.formControlMargin}>
                  <Grid item xs={12} sm={6}>
                    <Typography className={classes.label}>
                      EXPERIENCE
                    </Typography>
                    <input
                      type="number"
                      placeholder="0 YRS"
                      className="textInput-small"
                      name="experience"
                      onChange={formik.handleChange}
                      value={formik.values.experience}
                    ></input>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item sm={6}>
                <Grid container>
                  <Typography className={classes.basicInfo}>
                    Hospital Information
                  </Typography>
                </Grid>
                <Grid container className={classes.pt2}>
                  <Grid item xs={12} sm={12}>
                    <Typography className={classes.label}>
                      HOSPITAL NAME
                    </Typography>
                    <input
                      type="text"
                      placeholder="HOSPITAL NAME"
                      className="textInput-large"
                      name="hospitalName"
                      onChange={formik.handleChange}
                      value={formik.values.hospitalName}
                    ></input>
                  </Grid>
                </Grid>
                <Grid container className={classes.pt2}>
                  <Grid item xs={12} sm={6}>
                    <Typography className={classes.label}>
                      START DATE
                    </Typography>
                    <input
                      type="date"
                      name="startDate"
                      className="textInput-small"
                      value={formik.values.startDate}
                      onChange={formik.handleChange}
                    ></input>
                  </Grid>
                </Grid>
                <Grid container className={classes.pt2}>
                  <Typography className={classes.label}>PERMISSIONS</Typography>
                </Grid>
                <Grid container>
                  <Grid item>
                    <Button className={classes.adminBtn} onClick={handleAdmin}>
                      ADMIN
                    </Button>
                  </Grid>
                  <Grid item>
                    <Button
                      className={classes.chargeNurseBtn}
                      onClick={handleClickChargeNurse}
                    >
                      CHARGE NURSE
                    </Button>
                  </Grid>
                  <Grid item>
                    <Button className={classes.noneBtn} onClick={handleNone}>
                      NONE
                    </Button>
                  </Grid>
                </Grid>
                <Grid container className={classes.pt2}>
                  <Grid item xs={12} sm={12}>
                    <Typography className={classes.label}>
                      LOGIN PASSWORD
                    </Typography>
                    <input
                      type="text"
                      placeholder="PASSSWORD"
                      className="textInput-large"
                      name="password"
                      onChange={formik.handleChange}
                      value={formik.values.password}
                    ></input>
                  </Grid>
                </Grid>
                <Grid container className={classes.pt2}>
                  <Grid item>
                    <Typography className={classes.label}>
                      ASSIGNED UNITS
                    </Typography>
                  </Grid>
                </Grid>
                <Grid container spacing={2}>
                  <Grid item>
                    <input
                      type="text"
                      placeholder="ENTER"
                      className="textInput-small"
                      name="units"
                      onChange={formik.handleChange}
                      value={formik.values.units}
                    ></input>
                  </Grid>
                  <Grid item>
                    <Button
                      className="btn-medium"
                      onClick={(e) => handleAddUnits(e)}
                    >
                      ADD
                    </Button>
                  </Grid>
                </Grid>
                <Grid container>
                  {showUnits === true && (
                    <>
                      {unitData.map((data) => (
                        <Chip
                          key={data}
                          deleteIcon={
                            <ClearOutlinedIcon
                              className={classes.unitDeleteIcon}
                            />
                          }
                          variant="outlined"
                          label={data}
                          onDelete={handleDelete(data)}
                          className={classes.unitSet}
                        />
                      ))}
                    </>
                  )}
                </Grid>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
        <Grid container className={classes.pt2}>
          <Grid item sm={6}>
            {toEdit === false ? (
              <Grid container>
                <Grid item>
                  <Button className="btn-cancel" onClick={formik.handleReset}>
                    CANCEL
                  </Button>
                </Grid>
              </Grid>
            ) : (
              <Grid container spacing={2}>
                <Grid item>
                  <Button
                    className={classes.formRemoveBtn}
                    onClick={handleOpenRemove}
                  >
                    REMOVE
                  </Button>
                </Grid>
                <Grid item>
                  <Button
                    className={classes.formSuspendBtn}
                    onClick={handleClickSuspend}
                  >
                    SUSPEND USER
                  </Button>
                </Grid>
              </Grid>
            )}
          </Grid>
          <Grid item sm={6}>
            <Grid container justify="flex-end">
              <Grid item>
                <Button
                  type="submit"
                  className="btn-large"
                  onClick={formik.handleSubmit}
                >
                  SAVE
                </Button>
              </Grid>
            </Grid>
          </Grid>
        </Grid>
      </Container>
      {/* Remove popup */}
      <Grid>
        <Dialog
          maxWidth={"md"}
          modal={false}
          open={toRemove}
          onRequestClose={handleCloseRemove}
          className="dialog-container"
        >
          <DialogContent>
            <DialogContentText>
              <Typography align="center" className={classes.popupHeader}>
                Are you sure you want to remove [user]"
              </Typography>
            </DialogContentText>
            <Grid container justify="center" spacing={2}>
              <Grid item>
                <Button
                  variant="outlined"
                  className="btn-cancel"
                  onClick={handleCloseRemove}
                >
                  CANCEL
                </Button>
              </Grid>
              <Grid item>
                <Button variant="outlined" className="btn-delete">
                  YES
                </Button>
              </Grid>
            </Grid>
          </DialogContent>
        </Dialog>
      </Grid>
      {/* Suspend popup */}
      <Grid>
        <Dialog
          maxWidth={"md"}
          modal={false}
          open={toOpenSuspend}
          onRequestClose={handleClose}
          className="dialog-container"
        >
          <DialogContent>
            <DialogContentText>
              <Typography align="center" className={classes.popupHeader}>
                Are you sure you want to Suspend User
              </Typography>
              <Grid container justify="center" alignItems="center">
                <Grid item>
                  <Typography className={classes.popupHeader}>for</Typography>
                </Grid>
                <Grid item>
                  <input
                    placeholder="0"
                    className={classes.popupTextField}
                    type="text"
                  ></input>
                </Grid>
                <Grid item>
                  <Typography className={classes.popupHeader}>days?</Typography>
                </Grid>
              </Grid>
            </DialogContentText>
            <Grid container justify="center" spacing={2}>
              <Grid item>
                <Button
                  variant="outlined"
                  className="btn-cancel"
                  onClick={handleCloseSuspend}
                >
                  CANCEL
                </Button>
              </Grid>
              <Grid item>
                <Button variant="outlined" className="btn-delete">
                  YES
                </Button>
              </Grid>
            </Grid>
          </DialogContent>
        </Dialog>
      </Grid>
      {/* ChargeNurse popup */}
      <Grid>
        <Dialog
          maxWidth={"md"}
          modal={false}
          open={popup}
          onRequestClose={handleClose}
          className="dialog-container"
        >
          <DialogContent>
            <DialogContentText>
              <Typography align="center" className={classes.popupHeader}>
                Are you sure you want to grant{" "}
                {formik.values.firstName + " " + formik.values.lastName}
              </Typography>
              <Typography align="center" className={classes.popupHeader}>
                to Charge Nurse access?
              </Typography>
            </DialogContentText>
            <Grid container justify="center" spacing={2}>
              <Grid item>
                <Button
                  variant="outlined"
                  className="btn-cancel"
                  onClick={handleClose}
                >
                  CANCEL
                </Button>
              </Grid>
              <Grid item>
                <Button
                  variant="outlined"
                  className="btn-delete"
                  onClick={handleClickChargeNurseOk}
                >
                  YES
                </Button>
              </Grid>
            </Grid>
          </DialogContent>
        </Dialog>
      </Grid>
    </>
  );
};

export default AddNurseForm;
