import React, { useState,useEffect} from "react";
import ApiGet from "../../Api/Get";
import ApiPost from "../../Api/Post";
const hospitalName = "Cedars-Sinai Medical Center";
const tabValue = 1;

const useUnitsList = () => {
  const [value, setValue] = useState(tabValue);
  const [toOpen, setToOpen] = useState(false);
  const [toOpenSortby, settoSortby] = React.useState(null);
  const [sortOption, setSortOption] = useState("");
  const [toNewUnit, setToNewUnit] = useState(false);
  const [toEdit, setToEdit] = useState(false);
  const [unitslist, setUnitsList] = useState({ unitsDetails: [] });
  const hospitalId=sessionStorage.getItem("hospitalId");
  const [deleteOpen, setDeleteOpen] = useState(false);
  const [values, setValues] = useState({
    unitID: "",
  });
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const handleOpenSortby = (event) => {
    settoSortby(event.currentTarget);
  };
 
  const handleCloseSortby = (event) => {
    settoSortby(null);
  };
  const handleSortChange = (event, newValue) => {
    setSortOption(newValue);
  };
  const handleAddUnit = () => {
   
    setToNewUnit(true);
  };
  const handleEdit = () => {
    setToEdit(true);
  };

  const handleClickOpen = (id) => {
    setDeleteOpen(true);
    setValues({
      unitID: id,
    });
  };
  const handleDelete = () => {
    deleteUnit();
  };
 
  const handleClose = () => {
    setDeleteOpen(false);
  };
  useEffect(() => {
    getUnits();
  });
  async function getUnits() {
    let response = await fetch(ApiGet.getUnits + "/" + hospitalId+"/unit", {
      headers: {
        "content-type": "application/json",
        authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
      },
    });

    let responseJson = await response.json();
    setUnitsList({
      unitsDetails: responseJson.data,
    });
  }
  async function deleteUnit() {
    let response = await fetch(
      ApiPost.deleteUnit + "/" + hospitalId + "/unit" + "/"+ values.unitID,
      {
        method: "DELETE",
        headers: {
          "content-type": "application/json",
          authorization: `Bearer ${sessionStorage.getItem("UserToken")}`,
        },
      }
    );
    let responseJson = await response.json();
    setDeleteOpen(false);
  }
  return {
    value,
    toOpen,
    toOpenSortby,
    hospitalName,
    sortOption,
    toNewUnit,
    toEdit,
    unitslist,
    deleteOpen,
    values,
    handleChange,
    handleOpenSortby,
    handleClickOpen,
    handleCloseSortby,
    handleSortChange,
    handleAddUnit,
    handleEdit,
    handleDelete,
    handleClose
  };
};

export default useUnitsList;
