import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { useFormik } from "formik";
import ApiPost from "../Api/Post";

const isValidEmail = (email) =>
  /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email);
const LoginUseForm = () => {
  const history = useHistory();
  const [validateerrors, setValidateErrors] = useState({});
  const [continueError, setContinueError] = useState(false);
  const formik = useFormik({
    initialValues: {
      Email: "",
      Password: "",
    },
    onSubmit: async (values) => {
      if (values.Email === "") {
        setValidateErrors({
          ...validateerrors,
          Email: "Field Required!",
          Valid: true,
        });
      } else if (!isValidEmail(values.Email)) {
        setValidateErrors({
          ...validateerrors,
          Email: "Invalid Email!",
          Valid: true,
        });
      } else if (values.Password === "") {
        setValidateErrors({
          ...validateerrors,
          Password: "Field Required!",
          Valid: true,
        });
      } else {
        setContinueError(true);
        const response = await fetch(ApiPost.Login, {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            username: values.Email,
            password: values.Password,
            client_id: process.env.REACT_APP_CLIENT_ID,
            client_password: process.env.REACT_APP_CLIENT_PASSWORD,
          }),
        });
        const responseJson = await response.json();
        if (responseJson.response_code === 200) {
          sessionStorage.setItem("UserToken", responseJson.data.access_token);
          history.push(`/Hospitals`);
        } else {
          setValidateErrors({
            ...validateerrors,
            Password: "your email or password is incorrect!",
            Valid: true,
          });
        }
      }
    },
  });
  return {
    formik,
    validateerrors,
    continueError,
  };
};

export default LoginUseForm;
