import React from "react";
import {
  Button,
  Typography,
  Grid,
  Container,
  Paper,
  Dialog,
  DialogContent,
  DialogContentText,
  Divider,
  TextField,
} from "@material-ui/core";
import Autocomplete from "@material-ui/lab/Autocomplete";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { SketchPicker } from "react-color";
import { Redirect } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import variables from "../../variables.module.scss";
import useAddHospitalForm from "./useAddHospitalForm";
import "../../assets/style/styles.scss";

const AddHospitals = () => {
  const {
    formik,
    toOpen,
    toOpenDialog,
    toBack,
    handleClickOpen,
    handleClose,
    handleColorChange,
    handleDialogClickOpen,
    handleBackbtn,
    primaryColor,
    adminData,
    buttonId,
    adminName,
    handleAddAdmin,
    handleAdminChange,
    handleDeleteAdmin
  } = useAddHospitalForm();

  const useStyles = makeStyles({
    hpHd: {
      color: variables.darkColor,
      paddingTop: "3%",
      fontSize: "34px",
      fontFamily: variables.sfProDisplay,
      lineHeight: "41px",
      fontWeight: variables.fontwt600,
      fontStyle: "normal",
      display: variables.display,
    },
    hpAdmin: {
      color: variables.darkColor,
      fontFamily: variables.sfProText,
      fontSize: "28px",
      lineHeight: "33px",
      textAlign: "center",
    },
    hpSub: {
      color: variables.darkColor,
      fontSize: "15px",
      fontFamily: variables.sfProText,
      lineHeight: "18px",
      fontWeight: "600",
      display: "flex",
      fontStyle: "normal",
      paddingTop: "2%",
      paddingBottom: "2%",
      paddingLeft: "3%",
    },
    hpInf: {
      color: variables.darkColor,
      fontFamily: variables.sfProDisplay,
      fontSize: "22px",
      lineHeight: "26px",
      fontWeight: "600",
      display: "flex",
      fontStyle: "normal",
    },
    hpLabel: {
      fontWeight: variables.fontwt500,
      fontSize: variables.fontSize12,
      color: variables.darkColor,
      fontFamily: variables.sfProText,
      paddingTop: "1%",
    },
    //primary color button
    primaryBtn: {
      width: variables.width,
      height: variables.height,
      background: primaryColor.primaryColor,
      "&:hover": {
        backgroundColor: primaryColor.primaryColor,
      },
    },
    //secondary color button
    secondaryBtn: {
      width: variables.width,
      height: variables.height,
      background: primaryColor.secondaryColor,
      "&:hover": {
        backgroundColor: primaryColor.secondaryColor,
      },
    },
    //tertiary color button
    tertiaryBtn: {
      width: variables.width,
      height: variables.height,
      background: primaryColor.tertiaryColor,
      "&:hover": {
        backgroundColor: primaryColor.tertiaryColor,
      },
    },
    //color selector  button
    colorBtn: {
      width: variables.width,
      height: variables.height,
      border: "1px solid #D6D6D6",
      borderRadius: "4px",
      backgroundColor: variables.grayColor,
      "&:hover": {
        backgroundColor: variables.grayColor,
      },
    },
    adminHd: {
      color: variables.darkColor,
      fontWeight: variables.fontwt600,
      fontFamily: variables.sfProDisplay,
      fontSize: "22px",
      lineHeight: "26px",
    },
    addHd: {
      color: variables.darkColor,
      fontWeight: variables.fontWeight,
      fontFamily: variables.sfProText,
      fontSize: "12px",
      lineHeight: "14px",
    },
    addnewBtn: {
      width: "87px",
      height: "50px",
      fontFamily: variables.sfProText,
      backgroundColor: variables.tertiaryColor,
      "&:hover": {
        backgroundColor: variables.tertiaryColor,
      },
      color: variables.whiteColor,
    },
    hpGrid: {
      marginLeft: "4%",
      marginTop: "2%",
    },
    subGrid: {
      marginTop: "2%",
    },
    grid: {
      paddingTop: "2%",
    },
    colorGrid: {
      marginLeft: "2%",
      marginTop: "1%",
    },
    btnGrid: {
      paddingTop: "1%",
      paddingBottom: "1%",
    },
    icon: {
      color: variables.tertiaryColor,
      width: "16px",
      height: "16px",
    },
    adminInfo: {
      marginLeft: "23%",
      marginTop: "2%",
    },
    adminGrid: {
      marginTop: "2%",
      overflowY: "scroll",
      height: "250px",
    },
    adminName: {
      color: variables.secondaryColor,
      fontFamily: variables.sfProText,
      fontWeight: "bold",
    },
    adminDetails: {
      color: variables.darkColor,
      fontFamily: variables.sfProText,
    },
    deleteBtn: {
      color: variables.grayColor,
      textTransform: "lowercase",
      fontFamily: variables.sfProText,
    },
    autoList: {
      width: "286px",
    },
    autoName: {
      "&::placeholder": {
        fontSize: variables.fontSize15,
        fontWeight: variables.fontwt500,
        color: variables.darkColor,
        paddingLeft: "1%",
        fontFamily: variables.sfProText,
      },
    },
    paper: {
      borderRadius: "16px",
    },
  });

  const classes = useStyles();

  return (
    <>
      {toBack ? <Redirect to="/Hospitals" /> : null}
      <Container maxWidth="lg">
        <Grid className={classes.grid}>
          <Button
            edge="start"
            variant="outlined"
            onClick={handleBackbtn}
            className="btn-small"
          >
            <ArrowBackIcon className={classes.icon}></ArrowBackIcon>
          </Button>
        </Grid>
        <Grid>
          <Typography className={classes.hpHd}>Add New Hospital</Typography>
        </Grid>
        <Grid className={classes.grid}>
          <Paper className={classes.paper}>
            <Grid>
              <Typography className={classes.hpSub}> New Hospital</Typography>
            </Grid>
            <Divider className="divider" />
            <Grid container>
              <Grid item sm={6}>
                <Grid className={classes.hpGrid}>
                  <Typography className={classes.hpInf}>
                    Hospital Information
                  </Typography>
                  <Typography className={classes.hpLabel}>
                    HOSPITAL NAME
                  </Typography>
                  <input
                    name="name"
                    type="text"
                    placeholder="HOSPITAL NAME"
                    className="textInput-large"
                    value={formik.values.name}
                    onChange={formik.handleChange}
                  />
                  <Typography className={classes.hpLabel}>ADDRESS</Typography>
                  <input
                    id="address"
                    type="text"
                    name="address"
                    className="textInput-large"
                    placeholder="ADDRESS"
                    value={formik.values.address}
                    onChange={formik.handleChange}
                  />
                  <Grid container spacing={2}>
                    <Grid item sm={5}>
                      <Typography className={classes.hpLabel}>CITY</Typography>
                      <input
                        id="city"
                        name="city"
                        type="text"
                        className="textInput-small"
                        placeholder="CITY"
                        value={formik.values.city}
                        onChange={formik.handleChange}
                      />
                    </Grid>
                    <Grid item sm={6}>
                      <Grid>
                        <Typography className={classes.hpLabel}>
                          STATE
                        </Typography>
                        <input
                          type="text"
                          id="state"
                          name="state"
                          placeholder="STATE"
                          className="textInput-small"
                          value={formik.values.state}
                          onChange={formik.handleChange}
                        />
                      </Grid>
                    </Grid>
                  </Grid>
                  <Typography className={classes.hpLabel}>ZIP CODE</Typography>
                  <input
                    id="zipcode"
                    name="zipcode"
                    type="text"
                    placeholder="ZIP CODE"
                    className="textInput-small"
                    value={formik.values.zipcode}
                    onChange={formik.handleChange}
                  />
                  <Typography className={classes.hpLabel}>
                    BRAND COLORS
                  </Typography>
                  <Grid container spacing={1}>
                    <Grid item>
                      {primaryColor.primaryColor !== "" && (
                        <Button
                          className={classes.primaryBtn}
                          id="primary"
                          name="primary"
                        ></Button>
                      )}
                    </Grid>
                    <Grid item>
                      {primaryColor.secondaryColor !== "" && (
                        <Button
                          id="secondary"
                          name="secondary"
                          className={classes.secondaryBtn}
                        ></Button>
                      )}
                    </Grid>
                    <Grid item>
                      {primaryColor.tertiaryColor !== "" && (
                        <Button
                          id="tertiary"
                          name="tertiary"
                          className={classes.tertiaryBtn}
                        ></Button>
                      )}
                    </Grid>
                    <Grid item>
                      {buttonId !== 3 && (
                        <Button
                          onClick={handleClickOpen}
                          id="Colorbtn"
                          className={classes.colorBtn}
                        >
                          +
                        </Button>
                      )}
                    </Grid>
                  </Grid>
                  <Grid className={classes.colorGrid}>
                    {toOpen == true && (
                      <SketchPicker
                        size={200}
                        onChangeComplete={handleColorChange}
                      />
                    )}
                  </Grid>
                </Grid>
              </Grid>
              {/* Administrator Information */}
              <Grid item sm={6}>
                <Grid className={classes.adminInfo}>
                  <Grid>
                    <Typography className={classes.adminHd}>
                      Administrator Information
                    </Typography>
                  </Grid>
                  <Grid className={classes.adminGrid}>
                    {adminData.map((item) => (
                      <Grid key={item.id}>
                        <Grid>
                          <Typography className={classes.adminName}>
                            {item.first_name + " " + item.last_name}
                          </Typography>
                        </Grid>
                        <Grid>
                          <Typography className={classes.adminDetails}>
                            {item.email}
                          </Typography>
                        </Grid>
                        <Grid>
                          <Typography className={classes.adminDetails}>
                            {item.phone}
                          </Typography>
                        </Grid>
                        <Grid>
                          <Typography className={classes.adminDetails}>
                            {item.type}
                          </Typography>
                        </Grid>
                        <Grid>
                          <Button
                            className={classes.deleteBtn}
                            onClick={() => handleDialogClickOpen(item.id)}
                          >
                            Delete
                          </Button>
                        </Grid>
                        <Divider />
                      </Grid>
                    ))}
                  </Grid>
                  <Grid className={classes.subGrid}>
                    <Typography className={classes.addHd}>
                      ADD NEW ADMIN
                    </Typography>
                  </Grid>
                  <Grid
                    container
                    className={classes.subGrid}
                    spacing={1}
                    alignItems="center"
                  >
                    <Grid item>
                      <Autocomplete
                        id="admin"
                        options={adminName.adminName}
                        getOptionLabel={option => option.first_name + " "+ option.last_name}
                        // options={adminName.map((option) => option.name)}
                        className={classes.autoList}
                        onChange={(event,value)=>handleAdminChange(event,value)}
                        freeSolo
                        renderInput={(params) => (
                          <TextField
                            {...params}
                            variant="outlined"
                            placeholder="NAME"
                            className={classes.autoName}
                          />
                        )}
                      />
                    </Grid>
                    <Grid item>
                      <Button className={classes.addnewBtn} onClick={handleAddAdmin}>ADD NEW</Button>
                    </Grid>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Paper>
        </Grid>
        <Grid container className={classes.btnGrid}>
          <Grid item sm={7}>
            <Button className="btn-cancel" onClick={formik.handleReset}>
              CANCEL
            </Button>
          </Grid>
          <Grid>
            <Button className="btn-large" onClick={formik.handleSubmit}>
              ADD NEW HOSPITAL
            </Button>
          </Grid>
        </Grid>
        {/* Delete Administrator  */}
        <Grid>
          <Dialog
            maxWidth={"md"}
            modal={false}
            open={toOpenDialog}
            onRequestClose={handleClose}
            className="dialog-container"
          >
            <DialogContent>
              <DialogContentText>
                <Typography className={classes.hpAdmin}>
                  Are you sure you want to delete this administrator ?
                </Typography>
              </DialogContentText>
              <Grid container justify="center" spacing={2}>
                <Grid item>
                  <Button onClick={handleClose} className="btn-cancel">
                    CANCEL
                  </Button>
                </Grid>
                <Grid item>
                  <Button onClick={handleDeleteAdmin} className="btn-delete">
                    DELETE
                  </Button>
                </Grid>
              </Grid>
            </DialogContent>
          </Dialog>
        </Grid>
      </Container>
    </>
  );
};

export default AddHospitals;
