const AddUnitForm = () => {
  const {
    values,
    deletemodal,
    splitmodal,
    addroommodal,
    formik,
    validateerrors,
    continueError,
    showdesignationList,
    designationlist,
    permissionlist,
    showpermissionList,
    fields,
    toBack,
    handleNext,
    openDeleteModal,
    cancelDeleteModal,
    openSplitModal,
    cancelSplitModal,
    openAddroomModal,
    cancelAddroomModal,
    handleBack,
    handleAddDesignation,
    handleRemoveDesignation,
    handleAddPermission,
    handleRemovePermission,
    handleAddDimension,
    handleRemove,
    UnitsList,
    handleClickColor,
  } = UnitUseForm();
  const classes = useStyles();
  return (
    <>
      {toBack ? <Redirect to="/Units" /> : null}
      <form onSubmit={formik.handleSubmit} onChange={formik.handleChange}>
        <Grid>
          {values.pageValue === "Page1" && (
            <Grid>
              <Grid>
                <Button
                  edge="start"
                  variant="outlined"
                  onClick={UnitsList}
                  className={classes.arrowbackHeader}
                >
                  <ArrowBackIcon className={classes.arrrowbackheadericon} />
                </Button>
              </Grid>
              <Typography variant="h6" className={classes.header}>
                Add New Unit
              </Typography>
              <Card className={classes.card}>
                <Typography className={classes.Subheader}>New Unit</Typography>
                <Typography className={classes.headerborder}></Typography>
                <Grid container spacing={3}>
                  <Grid item xs={12} sm={5}>
                    <Grid>
                      <Typography className={classes.Label}>
                        Basic Information
                      </Typography>
                    </Grid>
                    <Grid className={classes.row}>
                      <Typography className={classes.SubLabel}>
                        UNIT NAME
                      </Typography>
                      <Typography className={classes.SubLabelnoofroom}>
                        NUMBER OF ROOMS
                      </Typography>
                    </Grid>
                    <Grid>
                      <input
                        error={continueError && formik.values.unitName === ""}
                        id="unitName"
                        name="unitName"
                        type="text"
                        className={classes.BasicInfoUnit}
                        placeholder="NAME"
                        onChange={formik.handleChange}
                        value={formik.values.unitName}
                      />

                      <input
                        id="NoofRooms"
                        name="noofRooms"
                        type="number"
                        className={classes.BasicInfoUnit}
                        placeholder="0"
                        onChange={formik.handleChange}
                        value={formik.values.noofRooms}
                      />
                      <Grid className={classes.row}>
                        <Typography className={classes.error}>
                          {validateerrors.unitName}
                        </Typography>
                      </Grid>
                    </Grid>
                    <Grid className={classes.pb}>
                      <Typography className={classes.SubLabel}>
                        UNIT COLORS
                      </Typography>
                    </Grid>
                    <Grid container spacing={2}>
                      <Button
                        className={classes.colorPaper1}
                        onClick={() => handleClickColor("#4b4b4b")}
                      ></Button>
                      <Button
                        className={classes.colorPaper2}
                        onClick={() => handleClickColor("#D6D6D6")}
                      ></Button>
                      <Button
                        className={classes.colorPaper3}
                        onClick={() => handleClickColor("#76787A")}
                      ></Button>
                      <Button
                        className={classes.colorPaper4}
                        onClick={() => handleClickColor("#DB2239")}
                      ></Button>
                    </Grid>
                  </Grid>
                  <Grid item xs={12} sm={7}>
                    <Typography className={classes.Label}>
                      Workload Dimensions
                    </Typography>
                    <Grid className={classes.row}>
                      <Typography className={classes.SubLabelnoofdimension}>
                        DIMENSION
                      </Typography>
                      <Typography className={classes.SubLabelnoofmin}>
                        MIN
                      </Typography>
                      <Typography className={classes.SubLabelnoofmax}>
                        MAX
                      </Typography>
                      <Typography className={classes.SubLabelshow}>
                        SHOW
                      </Typography>
                    </Grid>
                    {fields.map((field, idx) => {
                      return (
                        <Grid key={`${field}-${idx}`}>
                          <Grid>
                            <Grid className={classes.row}>
                              <Grid className={classes.checkbox}>
                                <FiberManualRecordOutlinedIcon />
                              </Grid>

                              <input
                                id="dimension1Value"
                                name="dimension1Value"
                                type="text"
                                className={classes.BasicInfoUnitdimension1}
                                placeholder="DIMENSION"
                                value={formik.values.dimension1Value}
                              />
                              <input
                                id="min1Value"
                                name="min1Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax1}
                                placeholder="0"
                                value={formik.values.min1Value}
                              />
                              <input
                                id="max1Value"
                                name="max1Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax1}
                                placeholder="0"
                                value={formik.values.max1Value}
                              />
                              <Grid>
                                <Checkbox className={classes.checkbox} />
                              </Grid>
                              <Button className={classes.mt}>
                                <DeleteIcon
                                  className={classes.deleteicon}
                                  onClick={() => handleRemove(idx)}
                                />
                              </Button>
                            </Grid>
                            <Grid className={classes.row}>
                              <Grid className={classes.checkbox}>
                                <ChangeHistoryOutlinedIcon />
                              </Grid>
                              <input
                                id="dimension2Value"
                                name="dimension2Value"
                                type="text"
                                className={classes.BasicInfoUnitdimension2}
                                placeholder="DIMENSION"
                              />
                              <input
                                id="min2Value"
                                name="min2Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax2}
                                placeholder="0"
                              />
                              <input
                                id="max2Value"
                                name="max2Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax2}
                                placeholder="0"
                              />
                              <Grid>
                                <Checkbox className={classes.checkbox} />
                              </Grid>
                              <Button className={classes.mt}>
                                <DeleteIcon className={classes.deleteicon} />
                              </Button>
                            </Grid>
                            <Grid className={classes.row}>
                              <Grid className={classes.checkbox}>
                                <BookmarkBorderIcon />
                              </Grid>
                              <input
                                id="dimension3Value"
                                name="dimension3Value"
                                type="text"
                                className={classes.BasicInfoUnitdimension3}
                                placeholder="DIMENSION"
                              />
                              <input
                                id="min3Value"
                                name="min3Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax3}
                                placeholder="0"
                              />
                              <input
                                id="max3Value"
                                name="max3Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax3}
                                placeholder="0"
                              />
                              <Grid>
                                <Checkbox className={classes.checkbox} />
                              </Grid>
                              <Button className={classes.mt}>
                                <DeleteIcon className={classes.deleteicon} />
                              </Button>
                            </Grid>
                            <Grid className={classes.row}>
                              <Grid className={classes.checkbox}>
                                <FolderOpenIcon />
                              </Grid>
                              <input
                                id="dimension4Value"
                                name="dimension4Value"
                                type="text"
                                className={classes.BasicInfoUnitdimension3}
                                placeholder="DIMENSION"
                              />
                              <input
                                id="min4Value"
                                name="min4Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax3}
                                placeholder="0"
                              />
                              <input
                                id="max4Value"
                                name="max4Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax3}
                                placeholder="0"
                              />
                              <Grid>
                                <Checkbox className={classes.checkbox} />
                              </Grid>
                              <Button className={classes.mt}>
                                <DeleteIcon className={classes.deleteicon} />
                              </Button>
                            </Grid>
                            <Grid className={classes.row}>
                              <Grid className={classes.checkbox}>
                                <PanoramaHorizontalIcon />
                              </Grid>
                              <input
                                id="dimension5Value"
                                name="dimension5Value"
                                type="text"
                                className={classes.BasicInfoUnitdimension3}
                                placeholder="DIMENSION"
                              />
                              <input
                                id="min5Value"
                                name="min5Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax3}
                                placeholder="0"
                              />
                              <input
                                id="max5Value"
                                name="max5Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax3}
                                placeholder=" 0"
                              />
                              <Grid>
                                <Checkbox className={classes.checkbox} />
                              </Grid>
                              <Button className={classes.mt}>
                                <DeleteIcon className={classes.deleteicon} />
                              </Button>
                            </Grid>
                            <Grid className={classes.row}>
                              <Grid className={classes.checkbox}>
                                <CropDinOutlinedIcon />
                              </Grid>
                              <input
                                id="dimension6Value"
                                name="dimension6Value"
                                type="text"
                                className={classes.BasicInfoUnitdimension3}
                                placeholder="DIMENSION"
                              />
                              <input
                                id="min6Value"
                                name="min6Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax3}
                                placeholder="0"
                              />
                              <input
                                id="max6Value"
                                name="max6Value"
                                type="text"
                                className={classes.BasicInfoUnitminmax3}
                                placeholder="0"
                              />
                              <Grid>
                                <Checkbox className={classes.checkbox} />
                              </Grid>
                              <Button className={classes.mt}>
                                <DeleteIcon
                                  className={classes.deleteicon}
                                  onClick={() => handleRemove(idx)}
                                />
                              </Button>
                            </Grid>
                          </Grid>
                        </Grid>
                      );
                    })}
                    <Grid className={classes.row}>
                      <Grid>
                        <Checkbox className={classes.rectangleicon} />
                      </Grid>
                      <Typography className={classes.Aggregated}>
                        Aggregated
                      </Typography>
                      <Button
                        // disabled="true"
                        variant="light"
                        className={classes.workload}
                        onClick={handleAddDimension}
                      >
                        ADD DIMENSION
                      </Button>
                    </Grid>
                  </Grid>
                </Grid>
                <Typography className={classes.border}></Typography>
                <Grid container spacing={3}>
                  <Grid item xs={12}>
                    <Typography className={classes.SpecifyDesignation}>
                      SPECIFY DESIGNATION
                    </Typography>
                    <Grid className={classes.row}>
                      <input
                        id="designation"
                        name="designation"
                        type="text"
                        className={classes.BasicInfoUnitdesignation}
                        placeholder="ENTER"
                      />
                      <Button
                        variant="light"
                        className={classes.workloaddesignation}
                        onClick={handleAddDesignation}
                      >
                        ADD
                      </Button>
                    </Grid>
                    {showdesignationList === true && (
                      <Grid>
                        <Card className={classes.dynamiccard}>
                          {designationlist.map((item) => (
                            <Grid>
                              <Typography className={classes.dynamictypography}>
                                {item}
                              </Typography>
                              <Button>
                                <ClearIcon
                                  className={classes.clearicon}
                                  onClick={() => handleRemoveDesignation(item)}
                                />
                              </Button>
                            </Grid>
                          ))}
                        </Card>
                      </Grid>
                    )}
                    <Typography className={classes.border}></Typography>
                  </Grid>
                </Grid>
                <Grid container spacing={3}>
                  <Grid item xs={12} sm={6}>
                    <Grid>
                      <Typography className={classes.LabelRatio}>
                        Clinical Nurses Ratio
                      </Typography>
                    </Grid>
                    <Grid>
                      <Typography className={classes.SubLabel}>
                        DAY SHIFT
                      </Typography>
                    </Grid>
                    <Grid>
                      <input
                        id="clinicalRatioDayPatients"
                        name="clinicalRatioDayPatients"
                        type="text"
                        className={classes.BasicInfoUnit}
                        placeholder="i.e. 4 PATIENTS"
                        onChange={formik.handleChange}
                      />
                      <input
                        id="clinicalRationDayNurse"
                        name="clinicalRationDayNurse"
                        type="text"
                        className={classes.BasicInfoUnitrooms}
                        placeholder="2 NURSES"
                        onChange={formik.handleChange}
                      />
                    </Grid>
                    <Grid>
                      <Typography className={classes.SubLabel}>
                        NIGHT SHIFT
                      </Typography>
                    </Grid>
                    <Grid>
                      <input
                        id="clinicalRatioNightPatients"
                        name="clinicalRatioNightPatients"
                        type="text"
                        className={classes.BasicInfoUnit}
                        placeholder="i.e. 4 PATIENTS"
                        onChange={formik.handleChange}
                      />
                      <input
                        id="clinicalRatioNightNurse"
                        name="clinicalRatioNightNurse"
                        type="text"
                        className={classes.BasicInfoUnitrooms}
                        placeholder="2 NURSES"
                        onChange={formik.handleChange}
                      />
                    </Grid>
                  </Grid>
                  <Grid item xs={12} sm={6}>
                    <Grid>
                      <Typography className={classes.LabelRatio}>
                        Nurse Aides Ratio
                      </Typography>
                    </Grid>
                    <Grid>
                      <Typography className={classes.SubLabel}>
                        DAY SHIFT
                      </Typography>
                    </Grid>
                    <Grid>
                      <input
                        id="nurseAideRatioDayPatient"
                        name="nurseAideRatioDayPatient"
                        type="text"
                        className={classes.BasicInfoUnit}
                        placeholder="i.e. 4 PATIENTS"
                        onChange={formik.handleChange}
                      />
                      <input
                        id="nurseAideRatioDayAide"
                        name="nurseAideRatioDayAide"
                        type="text"
                        className={classes.BasicInfoUnitrooms}
                        placeholder="1 AIDE"
                        onChange={formik.handleChange}
                      />
                    </Grid>
                    <Grid>
                      <Typography className={classes.SubLabel}>
                        NIGHT SHIFT
                      </Typography>
                    </Grid>
                    <Grid>
                      <input
                        id="nurseAideRatioNightPatient"
                        name="nurseAideRatioNightPatient"
                        type="text"
                        className={classes.BasicInfoUnit}
                        placeholder="i.e. 4 PATIENTS"
                        onChange={formik.handleChange}
                      />
                      <input
                        id="nurseAideRatioNightAide"
                        name="nurseAideRatioNightAide"
                        type="text"
                        className={classes.BasicInfoUnitrooms}
                        placeholder="1 AIDE"
                        onChange={formik.handleChange}
                      />
                    </Grid>
                  </Grid>
                </Grid>
                <Typography className={classes.border}></Typography>
                <Grid className={classes.pb}>
                  <Grid container spacing={3}>
                    <Grid item xs={12} sm={12}>
                      <Typography className={classes.SubLabel}>
                        GRANT/ASSIGN PERMISSION
                      </Typography>
                      <Grid>
                        <input
                          id="assignPermission"
                          name="assignPermission"
                          type="text"
                          className={classes.BasicInfoUnitassignPermission}
                          placeholder="ENTER"
                          value={formik.values.assignPermission}
                        />
                        <Button
                          variant="light"
                          className={classes.workloaddesignation}
                          onClick={handleAddPermission}
                        >
                          ADD
                        </Button>
                      </Grid>
                    </Grid>
                  </Grid>
                  {showpermissionList === true && (
                    <Card className={classes.dynamiccarddesignation}>
                      {permissionlist.map((item) => (
                        <Grid>
                          <Typography
                            className={classes.dynamictypographydesignation}
                          >
                            {item}
                          </Typography>
                          <Button>
                            <ClearIcon
                              className={classes.clearicondesignation}
                              onClick={() => handleRemovePermission(item)}
                            />
                          </Button>
                        </Grid>
                      ))}
                    </Card>
                  )}
                </Grid>
              </Card>
              <Grid container spacing={3}>
                <Grid item xs={12} sm={12}>
                  <Grid>
                    <Button onClick={UnitsList} className={classes.cancel}>
                      CANCEL
                    </Button>
                    <Button onClick={handleNext} className={classes.next}>
                      NEXT
                    </Button>{" "}
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          )}
        </Grid>
        <Grid>
          {values.pageValue === "Page2" && (
            <Grid>
              <Grid>
                <Button className={classes.arrowbackHeader} variant="outlined">
                  <ArrowBackIcon
                    onClick={handleBack}
                    className={classes.arrrowbackheadericon}
                  />
                </Button>
              </Grid>
              <Typography className={classes.header}>Add New Unit</Typography>
              <Grid container spacing={3}>
                <Grid item xs={12} sm={11}>
                  <Card className={classes.floormapcard}>
                    <Grid xs={12}>
                      <Typography className={classes.floormapSubheader}>
                        Create a Floor Map
                      </Typography>
                      <Grid className={classes.backicon}>
                        <UndoIcon />
                        <RedoIcon className={classes.redoIcon} />
                      </Grid>
                    </Grid>
                    <Typography className={classes.headerborder}></Typography>
                    <Grid className={classes.floormapgrid}>
                      <img
                        src={UnitFloorMap}
                        className={classes.floopmapimg}
                      ></img>
                      <Grid container spacing={5}>
                        <Grid xs={12} sm={6} md={4} lg={4} xl={3}>
                          <Card className={classes.floormapBox1}>01</Card>
                          <Card className={classes.floormapBox2}>02</Card>
                          <Card className={classes.floormapBox3}>
                            03a
                            <Grid className={classes.line}></Grid>
                            <Typography className={classes.floormapsplit3}>
                              03b
                            </Typography>
                          </Card>
                          <Card className={classes.floormapBox4}>04</Card>
                          <Card className={classes.floormapBox5}>05</Card>
                          <Card className={classes.floormapBox6}>06</Card>
                        </Grid>
                      </Grid>
                      <Grid xs={12} sm={6} md={4} lg={4} xl={3}>
                        <Typography
                          className={classes.floormapBox13}
                        ></Typography>
                        <Typography
                          className={classes.floormapBox12border}
                        ></Typography>
                        <Card className={classes.floormapBox7}>07</Card>
                        <Card className={classes.floormapBox8}>08</Card>
                        <Card className={classes.floormapBox9}>09</Card>
                        <Card className={classes.floormapBox10}>10</Card>
                        <Card className={classes.floormapBox11}>11</Card>
                        <Typography className={classes.floormapBox12}>
                          12
                        </Typography>
                      </Grid>
                    </Grid>
                  </Card>
                </Grid>
                <Grid className={classes.actions}>
                  <Card className={classes.cardaction}>
                    <Button
                      className={classes.modalicon}
                      onClick={openSplitModal}
                    >
                      <VerticalSplitIcon />
                    </Button>
                  </Card>
                  <Card className={classes.cardaction}>
                    <Button className={classes.modalicon}>
                      <Rotate90DegreesCcwIcon />
                    </Button>
                  </Card>
                  <Card className={classes.cardaction}>
                    <Button className={classes.modalicon}>
                      <EditIcon />
                    </Button>
                  </Card>
                  <Card className={classes.cardaction}>
                    <Button
                      className={classes.modalicon}
                      onClick={openDeleteModal}
                    >
                      <DeleteIcon />
                    </Button>
                  </Card>
                  <Card
                    className={classes.cardaction}
                    onClick={openAddroomModal}
                  >
                    <Button className={classes.modalicon}>
                      <AddCircleIcon />
                    </Button>
                  </Card>
                </Grid>
              </Grid>
              <Grid container spacing={3}>
                <Grid item xs={12}>
                  <Grid>
                    <Button className={classes.cancel} onClick={handleBack}>
                      CANCEL
                    </Button>
                    <Button type="submit" className={classes.submit} onClick={formik.handleSubmit}>
                      CREATE NEW UNIT
                    </Button>{" "}
                  </Grid>
                </Grid>
              </Grid>
              {/* DELETE MODAL */}
              <Dialog
                open={deletemodal}
                onClose={cancelDeleteModal}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
              >
                <DialogTitle className={classes.deletemodaltitle}>
                  {" "}
                  Delete this Object?
                </DialogTitle>
                <DialogContent></DialogContent>
                <DialogActions>
                  <Button
                    className={classes.modalcancel}
                    onClick={cancelDeleteModal}
                  >
                    CANCEL
                  </Button>
                  <Button className={classes.modalbtn} autoFocus>
                    DELETE
                  </Button>
                </DialogActions>
              </Dialog>

              {/* SPLIT MODAL */}
              <Dialog
                open={splitmodal}
                onClose={cancelSplitModal}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
              >
                <DialogTitle className={classes.splitmodaltitle}>
                  {" "}
                  Split Room?
                </DialogTitle>
                <DialogContent></DialogContent>
                <DialogActions>
                  <Button
                    className={classes.modalcancel}
                    onClick={cancelSplitModal}
                  >
                    CANCEL
                  </Button>
                  <Button className={classes.modalbtn} autoFocus>
                    YES
                  </Button>
                </DialogActions>
              </Dialog>
              {/* ADD ROOM MODAL */}
              <Dialog
                open={addroommodal}
                onClose={cancelAddroomModal}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
              >
                <Grid item xs={12}>
                  <Grid ClassName={classes.row}>
                    <DialogTitle className={classes.addroommodaltitle}>
                      Room Number
                    </DialogTitle>
                    <input
                      id="Email"
                      name="Email"
                      type="text"
                      className={classes.addnewroomfield}
                    />
                  </Grid>
                </Grid>
                <DialogContent></DialogContent>
                <DialogActions>
                  <Button className={classes.modaladdbtn} autoFocus>
                    SAVE
                  </Button>
                </DialogActions>
              </Dialog>
            </Grid>
          )}
        </Grid>
      </form>
    </>
  );
};
export default AddUnitForm;
