import React from "react";
import Nightingale from "../../images/Nightingale.png";
import { makeStyles } from "@material-ui/core/styles";
import { Typography, Grid, Card, Button } from "@material-ui/core";
import LoginUseForm from "./useLoginForm";
import variables from "../../variables.module.scss";
import "../../assets/style/styles.scss";

const useStyles = makeStyles((theme) => ({
  splitLeft: {
    height: "100%",
    left: "0",
    background: "linear-gradient(180deg, #199FC8 0%, #0B56A3 100%), #212121",
    width: "50%",
    position: "fixed",
    zindex: "1",
    top: "0",
    overflow: "hidden",
    paddingTop: "20px",
    boxShadow: "0px 4px 24px rgba(242, 153, 74, 0.4)",
  },
  splitRight: {
    height: "100%",
    right: "0",
    width: "50%",
    background: variables.lightColor,
    position: "fixed",
    zindex: "1",
    top: "0",
    overflow: "hidden",
    paddingTop: "20px",
  },

  loginCard: {
    display: "flex",
    flexDirection: "column",
    alignItems: "flex-start",
    padding: "30px 42px",
    position: "absolute",
    width: "auto",
    height: "60%",
    left: "15%",
    top: "20%",
    background: variables.whiteColor,
    borderRadius: "20px",
  },
  loginLabel: {
    color: variables.dark,
    fontSize: "13px",
    paddingTop: "20px",
    paddingBottom: "5px",
    fontWeight: variables.fontWeight,
  },
  loginTextField: {
    width: "400px",
    height: "40px",
    background: variables.lightColor,
    borderRadius: "8px",
    borderStyle: "none",
    "&:focus": {
      outline: "none",
      borderBottom: "2px solid #747474",
    },
    "&:invalid": {
      borderBottom: "1px solid #BF3A32",
    },
  },
  loginBtn: {
    width: "400px",
    height: "50px",
    background: variables.tertiaryColor,
    borderRadius: "8px",
    color: variables.whiteColor,
    borderStyle: "none",
    "&:focus": {
      outline: "none",
    },
    "&:hover": {
      background: variables.tertiaryColor,
    },
  },
  image: {
    width: "78px",
    height: "106.34px",
    marginLeft: "40%",
    marginTop: "30%",
  },

  error: {
    color: variables.redColor,
    fontWeight: variables.fontWeight,
    fontSize: "13px",
  },
  title: {
    color: variables.whiteColor,
    opacity: "0.9",
    marginLeft: "30px",
    marginTop: "10px",
  },
  Button: {
    paddingTop: "40px",
    paddingBottom: "30px",
  },
  Header: {
    color: variables.darkColor,
  },
}));

const Login = () => {
  const { validateerrors, formik, continueError } = LoginUseForm();
  const classes = useStyles();
  return (
    <form onSubmit={formik.handleSubmit} onChange={formik.handleChange}>
      <Grid className={classes.splitLeft}>
        <Typography variant="h6" className={classes.title} gutterBottom>
          Nightingale
        </Typography>
        <img className={classes.image} src={Nightingale}></img>
      </Grid>
      <Grid className={classes.splitRight}>
        <Card className={classes.loginCard}>
          <Typography variant="h5" className={classes.Header} gutterBottom>
            Welcome Back!
          </Typography>
          {/* EMAIL */}
          <Typography htmlFor="email" className={classes.loginLabel}>
            E-MAIL
          </Typography>
          <Grid>
            <input
              error={continueError && formik.values.Email === ""}
              id="Email"
              name="Email"
              type="text"
              //className={classes.loginTextField}
              className="textInput"
              onChange={formik.handleChange}
              value={formik.values.Email}
            />
            <Grid className={classes.error}>{validateerrors.Email}</Grid>
          </Grid>
          {/* PASSWORD */}
          <Typography htmlFor="password" className={classes.loginLabel}>
            PASSWORD
          </Typography>
          <Grid>
            <input
              error={continueError && formik.values.Password === ""}
              id="Password"
              name="Password"
              type="password"
              //className={classes.loginTextField}
              className="textInput"
              onChange={formik.handleChange}
              value={formik.values.Password}
            />
            <Grid className={classes.error}>{validateerrors.Password}</Grid>
          </Grid>
          <Grid className={classes.Button}>
            <Button type="submit" className="btn-large">
              SIGN IN
            </Button>
          </Grid>
        </Card>
      </Grid>
    </form>
  );
};

export default Login;
