import React, { useState, useEffect } from "react";
import {
  Box,
  Button,
  ButtonGroup,
  Container,
  Dialog,
  DialogContent,
  DialogContentText,
  Divider,
  Grid,
  Paper,
  Typography,
} from "@material-ui/core";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import { Formik } from "formik";
import { Redirect } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import useAddUserForm from "./useAddUserForm";
import variables from "../../variables.module.scss";
import "../../assets/style/styles.scss";

const useStyles = makeStyles((theme) => ({
  formContent: {
    paddingTop: "36px",
    paddingLeft: "32px",
    paddingRight: "24px",
  },
  formLabel: {
    fontFamily: variables.sfProText,
    fontSize: "12px",
    fontWeight: "500",
    color: variables.darkColor,
    marginLeft: "4px",
  },
  marginTop41px: {
    marginTop: "41px",
  },
  paper: {
    borderRadius: "12px",
    marginTop: "32px",
    paddingBottom: "50px",
  },
  buttonGroup: {
    "& .MuiButtonGroup-grouped": {
      minWidth: "143.44px",
    },
  },
  marginTop3pct: {
    marginTop: "3%",
  },
  resetButton: {
    backgroundColor: variables.tertiary,
    color: variables.whiteColor,
    fontFamily: variables.sfProText,
    "&.MuiButton-root": {
      maxWidth: "120px",
      minWidth: "120px",
      maxHeight: "40px",
      minHeight: "40px",
      backgroundColor: variables.tertiaryColor,
    },
  },
  marginTop5pct: {
    marginTop: "5%",
  },
  header: {
    fontSize: variables.fontSize34,
    fontWeight: variables.fontwt600,
    color: variables.darkColor,
    fontFamily: variables.sfProDisplay,
    fontStyle: "normal",
  },
  notSelectedBtn: {
    border: "1px solid #D6D6D6",
    borderRadius: "8px 0px 0px 8px",
    fontWeight: variables.fontwt500,
    fontSize: variables.fontSize12,
    color: variables.darkColor,
    height: "55px",
    width: "143.44px",
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
  selectedBtn: {
    backgroundColor: variables.tertiaryColor,
    borderRadius: "0px 8px 8px 0px",
    height: "55px",
    width: "143.44px",
    fontWeight: variables.fontwt500,
    fontSize: variables.fontSize12,
    color: variables.whiteColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
  },
  marginLeft16pct: {
    marginLeft: "16%",
  },
  text: {
    fontWeight: "500",
    color: variables.darkColor,
    fontFamily: variables.sfProText,
    fontsize: "18px",
    textAlign: "center",
  },
}));
const AddUser = () => {
  const {
    formik,
    toEdit,
    toOpen,
    toUsersList,
    selectedBtn,
    setSelectedBtn,
    handleBack,
    handleClickOpen,
    handleClose,
    handleAdministrator,
    handleModerator
  } = useAddUserForm();

  const classes = useStyles();
  return (
    <>
      {toUsersList ? <Redirect to="/Users" /> : null}
      <Container maxWidth="lg">
        <Grid container>
          <Button variant="outlined" className="btn-small" onClick={handleBack}>
            <ArrowBackIcon className="secondary-color"></ArrowBackIcon>
          </Button>
        </Grid>
        <Grid container className={classes.marginTop41px}>
          <Typography variant="h1" className={classes.header}>
            {toEdit ? "Manage Account" : "Add New User"}
          </Typography>
        </Grid>
        <Paper elevation={0} className={classes.paper}>
          <Typography className="form-title">NEW USER</Typography>
          <Divider className="divider" />
          <Formik>
            <Grid container className={classes.formContent}>
              <Grid item md={6}>
                {/* FIRST NAME */}
                <Grid container>
                  <Typography className={classes.formLabel}>
                    FIRST NAME
                  </Typography>
                </Grid>
                <Grid container>
                  <input
                    type="text"
                    placeholder="FIRST NAME"
                    name="firstName"
                    className="textInput-large"
                    value={formik.values.firstName}
                    onChange={formik.handleChange}
                  />
                </Grid>
                {/* LAST NAME */}
                <Grid container>
                  <Typography className={classes.formLabel}>
                    LAST NAME
                  </Typography>
                </Grid>
                <Grid container>
                  <input
                    type="text"
                    name="lastName"
                    placeholder="LAST NAME"
                    className="textInput-large"
                    value={formik.values.lastName}
                    onChange={formik.handleChange}
                  />
                </Grid>
                {!toEdit && (
                  <>
                    <Grid container>
                      <Typography className={classes.formLabel}>
                        PHONE NUMBER
                      </Typography>
                    </Grid>
                    <Grid container>
                      <input
                        type="text"
                        placeholder="PHONE NUMBER"
                        name="phoneNumber"
                        className="textInput-large"
                        value={formik.values.phoneNumber}
                        onChange={formik.handleChange}
                      />
                    </Grid>
                  </>
                )}
                <Grid container>
                  <Typography className={classes.formLabel}>ROLE</Typography>
                </Grid>
                <Grid container>
                  <ButtonGroup className={classes.buttonGroup}>
                    <Button className={classes.notSelectedBtn} onClick={handleAdministrator}>
                      SUPER ADMIN
                    </Button>
                    <Button className={classes.selectedBtn} onClick={handleModerator}>MODERATOR</Button>
                  </ButtonGroup>
                </Grid>
              </Grid>

              <Grid item md={6}>
                {/* EMAIL */}
                <Grid container>
                  <Typography className={classes.formLabel}>EMAIL</Typography>
                </Grid>
                <Grid container>
                  <input
                    type="text"
                    placeholder="EMAIL"
                    name="email"
                    className="textInput-large"
                    value={formik.values.email}
                    onChange={formik.handleChange}
                  />
                </Grid>
                {/* PASSWORD */}
                <Grid container>
                  <Typography className={classes.formLabel}>
                    PASSWORD
                  </Typography>
                </Grid>
                <Grid container>
                  <input
                    type="text"
                    placeholder="PASSWORD"
                    name="password"
                    className="textInput-large"
                    value={formik.values.password}
                    onChange={formik.handleChange}
                  />
                </Grid>
                <Grid container direction="column">
                  <Grid item className={classes.marginTop3pct}>
                    <Button
                      className={classes.resetButton}
                      onClick={formik.handleReset}
                    >
                      RESET
                    </Button>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Formik>
        </Paper>
        <Grid container className={classes.marginTop5pct}>
          <Grid item>
            {toEdit ? (
              <Button className="btn-cancel">
                DELETE ACCOUNT
              </Button>
            ) : (
              <Button className="btn-cancel" onClick={handleBack}>DELETE ACCOUNT</Button>
            )}
          </Grid>
          <Grid
            item
            className={classes.marginLeft16pct}
            onClick={formik.handleSubmit}
          >
            <Button className="btn-large">
              {toEdit ? "SAVE CHANGES" : "ADD NEW USER"}
            </Button>
          </Grid>
        </Grid>

        <Grid>
          <Dialog
            maxWidth={"md"}
            modal={false}
            open={toOpen}
            onRequestClose={handleClose}
            className="dialog-container"
          >
            <DialogContent>
              <DialogContentText>
                <Typography className={classes.text}>
                  An email has been sent to
                </Typography>
                <Typography align="center" className="dialog-content-text">
                  {formik.values.email}
                </Typography>
              </DialogContentText>
              <Grid container justify="center">
                <Grid item>
                  <Button
                    variant="outlined"
                    className="btn-delete"
                    onClick={handleBack}
                  >
                    EXIT
                  </Button>
                </Grid>
              </Grid>
            </DialogContent>
          </Dialog>
        </Grid>
      </Container>
    </>
  );
};

export default AddUser;
