import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  Button,
  FormControl,
  FormControlLabel,
  FormLabel,
  Grid,
  IconButton,
  Menu,
  Paper,
  Radio,
  RadioGroup,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Tabs,
  Typography,
  Dialog,
  DialogContent,
  DialogContentText,
} from "@material-ui/core";
import { BrowserRouter, Link, Redirect } from "react-router-dom";
import SortIcon from "@material-ui/icons/Sort";
import SearchIcon from "@material-ui/icons/Search";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import variables from "../../../variables.module.scss";
import useUnitsList from "./useUnitsList";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  main: {
    margin: 0,
    width: variables.fullWidth,
  },
  pageContent: {
    padding: "2%",
    backgroundColor: variables.backgroundColor,
  },
  borderRadius10: {
    borderRadius: "10px",
  },
  secondary: {
    color: variables.secondaryColor,
  },
  marginTop1pct: {
    marginTop: "1%",
  },
  marginTop2pct: {
    marginTop: "2%",
  },
  tabMainContainer: {
    marginTop: "1%",
    width: variables.fullWidth,
  },
  tabsContainer: {
    borderBottom: "4px solid #D6D6D6",
  },
  tabHover: {
    textDecoration: "none",
    cursor: "pointer",
  },
  tableContainer: {
    margin: "2%",
    width: "95%",
  },
  tableRow: {
    height: 20,
  },
  buttonAdd: {
    backgroundColor: variables.tertiary,
  },
  tableCell: {
    padding: "0px 16px",
  },
  gray84: {
    color: variables.grayColor
  },
  iconFontSize: {
    fontSize: "13px",
  },
  tertiaryColor: {
    color: variables.tertiaryColor,
  },
  fullWidth: {
    width: variables.fullWidth,
  },
  width5pct: {
    width: "5%",
  },
  width10pct: {
    width: "10%",
  },
  marginRight2pct: {
    marginRight: variables.marginRight2,
  },
  menuContainerWidth: {
    width: "450%",
  },
  sortMenuTitle: {
    color: variables.blackColor,
    fontSize: "1.5rem",
  },
  sortButton: {
    color: variables.whiteColor,
    fontSize: "15px",
    backgroundColor: variables.tertiaryColor,
    width: "180px",
    "&:hover": {
      backgroundColor: variables.tertiaryColor,
    },
  },
  indicator: {
    backgroundColor: variables.tertiaryColor,
    height: "4px",
  },
  addButton: {
    "& .MuiButton-label": {
      width: "120px",
      height: "35px",
      backgroundColor: variables.tertiaryColor,
      borderRadius: "4px",
      color: "white",
    },
    "&.MuiButton-root:hover": {
      backgroundColor: "transparent",
    },
  },
  textColorInherit: {
    opacity: 1,
  },
  units: {
    fontWeight: "500",
    color: variables.dimGrayColor,
    fontSize: "15px",
    lineHeight: "17.9px",
  },
  hospitalName: {
    color: variables.matterhornColor,
    fontWeight: "600",
    lineHeight: "40.57px",
    fontSize: "34px",
  },
  matterHornColor: {
    color: variables.matterhornColor,
  },
  tableRadius: {
    borderRadius: "16px",
  },
  tableHeaderBorder: {
    borderBottom: "4px solid #EBEBEB",
  },

    tableHead: {
      fontWeight: variables.fontwt700,
      color: "#3E3A37",
      fontSize: variables.fontSize12,
      fontFamily: variables.sfProText,
      fontStyle: "normal",
    },
  unitNameText: {
    color: variables.secondaryColor,
    fontSize: "15px",
    fontWeight: "700",
  },
  dialogContentSubHeader: {
    color: variables.dimGrayColor,
    fontFamily: variables.sfProText,
    fontStyle: "normal",
  },
}));

const paperMenu = {
  width: 220,
  borderRadius: 10,
  padding: "20px",
};

const UnitsList = () => {
  const {
    value,
    deleteOpen,
    toOpenSortby,
    hospitalName,
    data,
    sortOption,
    toNewUnit,
    handleChange,
    handleAddUnit,
    handleOpenSortby,
    handleClickOpen,
    handleCloseSortby,
    handleSortChange,
    toEdit,
    handleEdit,
    unitslist,
    handleDelete,
    handleClose
  } = useUnitsList();
  const classes = useStyles();
  return (
    <>
      {toNewUnit ? <Redirect to="/AddUnitForm" /> : null}
      {toEdit ? <Redirect to="/EditUnitForm" /> : null}
      <Grid container className={classes.pageContent}>
        {/* Search Button */}
        <Grid container>
          <Button edge="start" variant="outlined">
            <SearchIcon className={classes.secondary}></SearchIcon>
          </Button>
        </Grid>
        <Grid container className={classes.marginTop1pct}>
          <Grid item>
            <Typography className={classes.units}>Units</Typography>
          </Grid>
        </Grid>
        <Grid container className={classes.marginTop1pct}>
          <Grid item>
            <Typography className={classes.hospitalName}>
              {hospitalName}
            </Typography>
          </Grid>
        </Grid>

        {/* Tabs */}
        <Grid className={classes.tabMainContainer}>
          <Tabs
            value={value}
            variant="fullWidth"
            textColor="primary"
            onChange={handleChange}
            classes={{ indicator: classes.indicator }}
          >
            <Tab
              label="UNITS"
              to="/Units"
              component={Link}
              className={classes.tabsContainer}
            />
            <Tab label="STAFF" className={classes.tabsContainer} />
            <Tab label="PATIENTS" className={classes.tabsContainer} />
            <Tab label="BASIC INFORMATION" className={classes.tabsContainer} />
            <Tab
              label="ADD NEW"
              component={Button}
              onClick={handleAddUnit}
              className={classes.addButton + " " + classes.tabsContainer}
              classes={{ textColorInherit: classes.textColorInherit }}
            />
          </Tabs>
        </Grid>
        {/* Table-Units Data */}
        <Grid className={classes.fullWidth + " " + classes.marginTop2pct}>
          <TableContainer component={Paper} className={classes.tableRadius}>
            <Table>
              <colgroup>
                <col className={classes.width10pct} />
                <col className={classes.width10pct} />
                <col className={classes.width10pct} />
                <col className={classes.width10pct} />
                <col className={classes.width5pct} />
              </colgroup>
              <TableHead className={classes.tableHeaderBorder}>
                <TableRow className={classes.tableRow}>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>
                      UNIT NAME
                    </Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>BEDS</Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>
                      NURSE RATIO (DAY, NIGHT)
                    </Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <Typography className={classes.tableHead}>
                      AIDE RATIO (DAY, NIGHT)
                    </Typography>
                  </TableCell>
                  <TableCell className={classes.tableCell}>
                    <IconButton
                      className={classes.iconFontSize}
                      onClick={handleOpenSortby}
                    >
                      <SortIcon className={classes.tertiaryColor}></SortIcon>
                      <Typography className={classes.tertiaryColor}>
                        SORT BY
                      </Typography>
                    </IconButton>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {unitslist.unitsDetails.map((item) => (
                  <TableRow key={item.id} className={classes.tableRow}>
                    <TableCell className={classes.tableCell}>
                      <Typography className={classes.unitNameText}>
                        {item.name}
                      </Typography>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      {item.rooms}
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      {item.nurse_ratio_nurse_day},{item.nurse_ratio_nurse_night}
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      {item.aides_ratio_patient_day},{item.aides_ratio_patient_night}
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      <IconButton>
                        <EditIcon onClick={handleEdit} className={classes.gray84} />
                      </IconButton>
                      <IconButton onClick={()=>handleClickOpen(item.id)}>
                        <DeleteIcon className={classes.gray84} />
                      </IconButton>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
          {/* Pagination */}
          <Grid container justify="flex-end">
            <Grid item className={classes.marginRight2pct}>
              Showing Page 1 of 3{" "}
              <IconButton>
                <ArrowForwardIosIcon className={classes.tertiaryColor} />
              </IconButton>
            </Grid>
          </Grid>
        </Grid>
         {/* Delete popup */}
         <Grid>
          <Dialog
            maxWidth={"md"}
            modal={false}
            open={deleteOpen}
            // onRequestClose={handleClose}
            className="dialog-container"
          >
            <DialogContent>
              <DialogContentText>
                <Typography className="dialog-content-text">
                  Are you sure want to delete this unit?
                </Typography>
                <Typography
                  align="center"
                  className={classes.dialogContentSubHeader}
                >
                  This cannot be undone
                </Typography>
              </DialogContentText>
              <Grid container justify="center" spacing={2}>
                <Grid item>
                  <Button
                    variant="outlined"
                    className="btn-cancel"
                     onClick={handleClose}
                  >
                    CANCEL
                  </Button>
                </Grid>
                <Grid item>
                  <Button variant="outlined" className="btn-delete" onClick={handleDelete}>
                    DELETE
                  </Button>
                </Grid>
              </Grid>
            </DialogContent>
          </Dialog>
        </Grid>
        {/* Sortby */}
        <Grid>
          <Menu
            className="userMenuDropdown"
            id="simple-menu"
            anchorEl={toOpenSortby}
            keepMounted
            className={classes.menuContainerWidth}
            open={Boolean(toOpenSortby)}
            PaperProps={{
              style: paperMenu,
            }}
          >
            <FormControl component="fieldset">
              <FormLabel className={classes.sortMenuTitle}>Sort By</FormLabel>
              <RadioGroup
                aria-label="Sortby"
                name="sort"
                value={sortOption}
                onChange={handleSortChange}
              >
                <FormControlLabel
                  value="unitName"
                  control={
                    <Radio disableRipple className={classes.secondary} />
                  }
                  label="Name"
                />
                <FormControlLabel
                  value="beds"
                  control={
                    <Radio disableRipple className={classes.secondary} />
                  }
                  label="Beds"
                />
                <FormControlLabel
                  value="nurseRatio"
                  control={
                    <Radio disableRipple className={classes.secondary} />
                  }
                  label="Nurse Ratio"
                />
                <FormControlLabel
                  value="AideRatio"
                  control={
                    <Radio disableRipple className={classes.secondary} />
                  }
                  label="Aide Ratio"
                />
              </RadioGroup>
              <Grid container justify="center">
                <Grid item>
                  <Button
                    variant="outlined"
                    className={classes.sortButton}
                    onClick={handleCloseSortby}
                  >
                    SORT
                  </Button>
                </Grid>
              </Grid>
            </FormControl>
          </Menu>
        </Grid>
      </Grid>
   </>
  );
};

export default UnitsList;
